.skeleton-recommendation-loader {

  /* Card styles */
  .card {
    background-color: #fff;
    height: 100%;
    width: 100%;
    overflow: hidden;
    border-radius: 12px;
    // box-shadow: 9px 17px 45px -29px rgba(0, 0, 0, 0.44);
  }

  /* Card image loading */
  .card__image img {
    width: 100%;
    height: 100%;
  }

  .card__image.loading {
    height: 280px;
    width: 400px;
  }

  /* Card title */
  .card__title {
    padding: 8px;
    font-size: 22px;
    font-weight: 700;
  }

  .card__title.loading {
    height: 1rem;
    width: 50%;
    border-radius: 3px;
  }

  /* Card description */
  .card__description {
    padding: 8px;
    font-size: 16px;
  }

  .card__description.loading {
    height: 3rem;
    border-radius: 3px;
  }

  .card__price.loading {
    height: 1rem;
    width: 20%;
    border-radius: 3px;
  }

  .card__quantity.loading {
    height: 20px;
    width: 50%;
    border-radius: 3px;
  }

  .card__btn.loading {
    height: 20px;
    width: 30px;
    border-radius: 50%;
  }

  .card__add.loading {
    height: 44px;
    width: 44px;
    border-radius: 50%;
  }

  /* The loading Class */
  .loading {
    position: relative;
    background-color: #e2e2e2;
  }

  /* The moving element */
  .loading::after {
    display: block;
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    transform: translateX(-100%);
    background: -webkit-gradient(linear, left top,
        right top, from(transparent),
        color-stop(rgba(255, 255, 255, 0.2)),
        to(transparent));

    background: linear-gradient(110deg, transparent,
        rgba(255, 255, 255, 0.2), transparent);

    // /* Adding animation */
    animation: loading 0.8s infinite;
  }

  /* Loading Animation */
  @keyframes loading {
    100% {
      transform: translateX(100%);
    }
  }
}