@import "../../../../../assets/scss/colors.scss";
@import "../../../../../assets/scss/cdn-assets-links.scss";

.bookmarklet-section {
  border-radius: 6px;
  background-color: $icon-light-info;
  cursor: pointer;

  .bookmarklet-link {
    color: $text-link
  }
}

.bookmarlet-model {
  .m-a-heading {
    color: $grey-0 !important;
  }

  .primary-btn {
    text-decoration: none;
    color: $grey-2 !important;
    cursor: grab;
  }

  .add-bookmarklet-btn-icon {
    height: 24px;
    width: 35.9px;
    display: block;
    cursor: grab;
    background-image: url($icon-logo-elephant);
    background-size: 35.9px;
    background-position: center;
    background-repeat: no-repeat;
  }

  .bookmarklet-steps {
    border-radius: 6px;
    border: solid 1px $grey-5;
    background-color: $brand-light;
    margin-right: 88px;
    margin-left: 88px;

    .steps-title {
      .steps-details {
        color: $grey-1;
      }
    }

    ol {
      padding-right: 70px;
      padding-left: 70px;
    }
  }
}

.order-list-bookmarklet {
  list-style: revert !important;
}

.bookmarklet-link {
  color: $text-link;
  cursor: pointer;

  &:hover {
    color: $text-link;
  }
}

.bookmarklet-disable-btn {
  color: $grey-1 !important;
  background-color: $grey-7;
  text-decoration: none;
  cursor: pointer;

  &:hover {
    text-decoration: none;
    color: $grey-0 !important;
    background-color: $grey-5;
  }
}