@import "../../../../assets/scss/colors.scss";
@import "../../../../assets/scss/breakpoints.scss";


.view-guest-header {
  background-color: $grey-2;

  .view-guest-text {
    color: $brand-light !important;
  }

  .view-guest-link {
    cursor: pointer;
  }
}

.fixed-view-registry-header {
  position: fixed;
  top: 0;
  z-index: 15;

  .view-registry-header {
    .header-registry-detail {
      background-color: rgba(255, 255, 255, 0.9);
      border-radius: 4px;
      border: 2px solid $grey-6;
    }

    &,
    .header-registry-detail {
      position: unset;
    }
  }
}

.view-registry-header {
  position: relative;
  background-size: cover;

  &.view-registry-header-height {
    height: 250px;
  }

  img {
    width: 100%;
    height: inherit;
    object-fit: cover;
  }

  .cover-pic-update-options {
    position: absolute;
    top: 24px;
    width: 100%;

    .update-cover-options {
      background-color: $grey-7;
      border-radius: 3px;
      font-size: 20px;
      border: solid 1px transparent;
      height: 36px;
      width: 36px;
    }

    .view-registry-cover-option-active {
      background-color: $brand-light;
      color: $grey-1;
      border: solid 1px $grey-1;
    }
  }

  .header-registry-detail {
    position: absolute;
    bottom: 0;
    width: 100%;

    .view-registry-header-border {
      background-color: rgba(255, 255, 255, 0.9);
      border-radius: 4px;
      border: 2px solid $grey-6;
    }

    .edit-name-icon {
      cursor: pointer;
      color: $grey-2;

      @include media-breakpoint-down(sm) {
        color: $grey-4;
        font-size: 17px;
        background-color: $grey-7;
        border-radius: 2px;
      }
    }

    .view-registry-header-displayName{
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    .view-registry-header-greeting {
      color: $grey-1;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;

      @include media-breakpoint-up(sm) {
        -webkit-line-clamp: 2;
      }
    }
  }

  .add-greeting-link {
    color: $text-link;
    cursor: pointer;
  }

  .view-and-share-link {
    color: $text-link;
    cursor: pointer;
    text-decoration: underline;
    text-decoration-color: $text-link;
  }

  .registry-status {
    cursor: pointer;
    border-radius: 3px;
    // border-right: 1px solid $grey-4;

    .registry-status-title {
      color: $text-link;
    }

    .public-status-dot {
      width: 11px;
      height: 11px;
      border-radius: 50%;
      background-color: $icon-success;
    }

    .private-status-dot {
      width: 11px;
      height: 11px;
      border-radius: 50%;
      background-color: #fa9993;
    }

    &:hover {
      background-color: $grey-7;
    }
  }

  .header-buttons {
    text-align: right;

    @include media-breakpoint-down(sm) {
      text-align: left;
    }
  }
}