@import "../../../../assets/scss/colors.scss";

.sort-section-filter {
  position: relative;
  width: 147px;
  cursor: pointer;

  .sort-filter-btn {
    text-decoration: none;
    border: 1px solid $grey-5;
    background-color: $brand-light;
    border-radius: 4px;
    outline: none;

    &:active, &:focus {
      & {
        border-radius: 4px 4px 0 0 !important;
        border-color: $grey-1 !important;

        .icon-chevron-down {
          transform: rotate(-180deg);
          -webkit-transform: rotate(-180deg);
          -ms-transform: rotate(-180deg);
        }
      }

      & ~ .filter-content {
        display: block;
      }
    }

    &:hover {
      border-color: $grey-1;
    }

    .selected-sort-text {
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
    }
  }

  .icon-chevron-down {
    font-size: 18px;
    color: $grey-4;

    transition: .3s;
    -moz-transition: .3s;
    -webkit-transition: .3s;
    -o-transition: .3s;
  }

  .filter-content {
    position: absolute;
    right: 0;
    top: 95%;
    border: 1px solid $grey-1;
    //width: max-content;
    display: none;
    width: 147px;
    z-index: 350;
    background-color: $brand-light;
    color: $grey-0;
    border-top: none;
    border-radius: 0 0 4px 4px;

    &.hide-filter-content{
      display: none !important;
    }

    &:hover{
      display: block;
    }

    .filter-item {
      text-align: start;

      &:hover {
        background-color: $grey-6;
      }
    }
  }
}