@import "../../../../assets/scss/colors.scss";
@import "../../../../assets/scss/custom-variables.scss";
@import "../../../../assets/scss/breakpoints.scss";
@import "../../../../assets/scss/cdn-assets-links.scss";
@import '../../../../assets/scss/mixins.scss';

.guest-tile-card {
  position: relative;
  border-radius: 6px;
  border: solid 1px $grey-5;
  background-color: $brand-light;
  cursor: pointer;

  .product-image {
    overflow: hidden;
    height: 274px;
    background-color: $brand-light;

    img {
      width: 100%;
      object-position: center;
      object-fit: cover;
      height: 274px;

      @include media-breakpoint-down(sm) {
        width: 100%;
      }
    }
  }

  .guest-tile-card-image {
    .guest-must-have {
      position: absolute;
      left: 0;
      top: 3 * $spacer;
    }
  }

  .added-gift-must-have-btn {
    border: none;
    outline: none !important;
    border-radius: 4px;
  
    .added-gift-must-have-text {
      line-height: 2;
    }
  
  }

  .guest-tile-pills-section {
    height: 92px;
  }

  .guest-product-title {
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 2;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    color: $grey-0;
    height: 56px;

    @include media-breakpoint-down(sm) {
      height: 52px;
    }
  }

  .guest-product-price {
    color: $grey-0;
    height: 24px;
  }

  .guest-tile-give-this-gift-btn {
    color: $grey-1 !important;
    background-color: $brand-light;
    border-radius: 2px;
    border: solid 1px $grey-5;
  }

  .guest-toggle-pills {
    width: max-content;
    border-radius: 4px;
    padding-top: 2px;
    padding-bottom: 2px;
    border-radius: 20px;
    border: solid 1px transparent;
    background-color: $icon-light-info;

    i {
      color: $grey-4;
      font-size: 12px;
    }

    .guest-icon-active {
      background-image: url($cdn-icon-group-gift-selected-svg);
      background-size: 12px;
      height: 13px;
      width: 13px;
      background-repeat: no-repeat;
      background-position: center;
    }

    .guest-icon-text {
      color: $grey-0 !important;
      @include font(11px, 16px)
    }

    .received-amount {
      color: $grey-0 !important;
    }

    .total-amount {
      color: $grey-1 !important;
    }
  }

  .group-gift-background {
    position: relative;
    width: 250px;
    border-radius: 50px;
    height: 4px;
    background-color: $grey-7;
  }

  .group-gift-progress {
    position: absolute;
    top: 0;
    height: 4px;
    border-radius: 50px;
    background-color: $brand-main;
  }

  &:hover {
    background-color: #f8fdfd;

    .guest-tile-give-this-gift-btn {
      border: solid 1px $tiffany-blue;
    }
  }
}