@import "../../../../assets/scss/colors.scss";
@import "../../../../assets/scss/custom-variables.scss";
@import "../../../../assets/scss/mixins.scss";
@import "../../../../assets/scss/cdn-assets-links.scss";
@import "../../../../assets/scss/breakpoints.scss";

.added-gift-must-have-btn {
  border: none;
  outline: none !important;
  border-radius: 4px;

  .added-gift-must-have-text {
    line-height: 2;
  }
}

.added-card {
  .added-gift-must-have-active {
    @include media-breakpoint-down(sm) {
      background-color: transparent;
      border: none;
    }

    background-color: $icon-light-error;
    color: $grey-0;
  }

  .added-gift-must-have-not-active {
    @include media-breakpoint-down(sm) {
      background-color: transparent;
    }

    background-color: $grey-7;
    color: $grey-1;
  }
}

.icon {
  height: 19px;
  width: 19px;
  background-size: 18px;
  background-repeat: no-repeat;
  background-position: center;
  background-color: transparent;
  border: none;
  outline: none !important;
}

.heart {
  background-image: url($cdn-icon-heart-svg);
}

.heart-fill {
  background-image: url($cdn-icon-heart-filled-svg);
}

.added-card {
  border-top: 2px solid $grey-7;
  // border-bottom: 2px solid $grey-7;
  background-color: $brand-light;

  .category {
    color: $grey-1;
  }

  .strick-price {
    text-decoration: line-through;
    color: $input-required;
  }

  .quantity-mul-text {
    color: $grey-2;
  }

  .added-card-no-product-image {
    // TODO: Change this default image if necessary
    @include no-product-image(84px, 100%, 42px);

    // Custom added not present in design
    @include media-breakpoint-down(sm) {
      @include no-product-image(84px, 50%, 42px);
    }
  }

  .added-card-buttons {
    @include media-breakpoint-down(sm) {
      position: absolute;
      right: 0;
    }
  }
}

.default-added-card-hover {
  &:hover {
    box-shadow: 0 8px 16px 0 rgba(74, 74, 74, 0.1), 0 0 1px 0 rgba(74, 74, 74, 0.15);
    background-color: #fbfbfb;
    cursor: pointer;
  }
}

.view-registey-added-card-hover {
  &:hover {
    background-color: rgba(186, 235, 238, 0.1);
    cursor: pointer;

    @include media-breakpoint-up(xl){
      padding-top: 0px !important;
    }

    .drag-icon {
      margin-bottom: 8px !important;
      display: block;
    }
  }
}

.product-img {
  max-width: 100%;
  height: 116px;

  &.added-card-product-image-radius {
    border-radius: 6px;
    border: solid 1px $grey-5;
  }
}

.drag-icon {
  height: 22px;
  width: 22px;
  display: none;
  cursor: grab;
  background-image: url($icon-drag-drop);
  background-size: 21px;
  background-position: center;
  background-repeat: no-repeat;
  opacity: .7;

  &.disabled-drag {
    opacity: .3;
  }

  .drag-drop-popover {
    font-style: initial;
  }

  .m-a-popover-content {
    right: -100% !important;
  }
}

.added-card-recommended {
  .category-recommended-products {
    max-height: 80vh;
    overflow-y: auto;
  }

  .added-card-recommends-bottom-gradient {
    position: absolute;
    bottom: 0;
    height: 50px;
    width: 100%;
    background-image: linear-gradient(to top, $brand-light, rgba(255, 255, 255, 0));
  }
}

.added-card-other-category {
  color: $grey-1;
  background-color: $grey-7;
  border-top: 2px solid $grey-6;
  // border-bottom: 2px solid $grey-6;

  &:last-child {
    border-bottom: 2px solid $grey-6;
  }

  .title-link {
    cursor: pointer;
    color: $text-link;
  }

  &:hover {
    background-color: $grey-6;
  }
}

//  Cash Fund Progress
.added-card-group-gift-progress-bar {
  border-radius: 50px;
  background-color: $grey-6;
  position: relative;
  height: 4px;
  width: 30%;

  .added-card-group-gift-progress {
    position: absolute;
    top: 0;
    left: 0;
    background-color: $tiffany-blue;
    border-radius: 50px;
    height: inherit;
  }
}