@import "../../../../assets/scss/breakpoints.scss";
@import "../../../../assets/scss/colors.scss";
@import "../../../../assets/scss/cdn-assets-links.scss";

.checklist-container {
  @include media-breakpoint-down(md) {
    position: fixed !important;
    width: auto;
    left: 0;
    right: 0;
    bottom: 0px;
    z-index: 10;
    background-color: white;
    box-shadow: 0 0 24px 0 rgba(74, 74, 74, 0.1), 0 0 1px 0 rgba(74, 74, 74, 0.15);
    height: 60px;
    padding-top: 20px;
    overflow: hidden;
    transition: height 0.5s;

    .checklist-container-icon-arrow {
      height: 20px;
      width: 20px;
      z-index: 10;
      background: #ffffff
        url($icon-chevron-right-svg)
        no-repeat center;
      -webkit-transition-duration: 0.4s;
      -moz-transition-duration: 0.4s;
      -o-transition-duration: 0.4s;
      transition-duration: 0.4s;
      -webkit-transition-property: -webkit-transform;
      -moz-transition-property: -moz-transform;
      -o-transition-property: -o-transform;
      transition-property: transform;
      transform: rotate(0deg);
      -webkit-transform: rotate(0deg);
      -ms-transform: rotate(0deg);
      display: none;
      @include media-breakpoint-down(lg) {
        display: inline-block;
        position: absolute;
        right: 10px;
      }
    }

    &.opened {
      height: calc(100vh - 75px);
      overflow: scroll;
      z-index: 200;
      .checklist-container-icon-arrow {
        transform: rotate(90deg);
        -webkit-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
      }

      .checklist-title {
        font-size: 20px;
      }
    }
    
    .divider-div{
      width: 100%;
      border-bottom: 0.5px solid $grey-5;
    }
  }
}

.tour-bg{
  border-radius: 12px;
  box-shadow: 0 20px 60px 0 rgba(74, 74, 74, 0.2), 0 0 5px 0 rgba(74, 74, 74, 0.15);
  border: solid 5px $brand-main;
  background-color: $brand-light;
  opacity: 0.8;
}

.checklist-scrollarea{
  overflow: scroll;
  height: 100vh;
  
  &::-webkit-scrollbar {
    display: none;
  }
}