@import "../../../../../assets/scss/typography.scss";
@import "../../../../../assets/scss/custom-variables.scss";
@import "../../../../../assets/scss/colors.scss";

.default-image-card {
  position: relative;
  border-radius: 6px;
  border: solid 1px transparent;

  img {
    width: 100%;
    height: 120px;
    object-fit: cover;
    cursor: pointer;
  }

  &[data-selected="false"] {
    &:hover {
      &::before {
        content: "Select Image";
        @extend .font-medium;
        @extend .text-sm;
        position: absolute;
        top: $spacer * 2;
        left: $spacer * 2;
      }

      border: solid 1px $grey-1;
    }
  }

  &[data-selected="true"] {
    .selected-image {
      position: absolute;
      top: 0;
      left: 0;
      font-size: 14px;
      color: $brand-light;
      background-color: $grey-1;
      border-bottom-right-radius: 4px;
    }
    border: solid 2px $grey-1;
  }
}