@import "src/assets/scss/mixins.scss";
@import "src/assets/scss/colors.scss";
@import "../../assets/scss/breakpoints.scss";
@import "../../assets/scss/cdn-assets-links.scss";

.onboarding-font-1 {
  @include font(16px, 22.4px, 0.23px);
}

.onboarding-font-2 {
  @include font(13px, 22.4px, 0.19px);
}

.onboarding-font-3 {
  @include font(14px, 22.4px, 0.2px);
}

.onboarding-font-4 {
  @include font(28px, 38.4px, 0.4px);
}

.onboarding-font-5 {
  @include font(10px, 14.2px, 0.14px);
}

.baby-registry-onboarding {
  background-color: #ffffff;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 9999;
  overflow-y: auto;

  .onboarding-background-color {
    height: 100vh;
    width: 100vw;
    overflow-y: auto;
    -ms-overflow-y: auto;
    background-color: $onboarding-background;
  }

  .onboading-text-color {
    color: $onboarding-text;
  }

  .onboarding-link-color {
    color: $onboarding-link
  }

  .onboarding-option-recommend-text {
    position: absolute;
    top: -20px;
    left: 50%;
    transform: translateX(-50%);
  }

  .onboarding-option-half-border {
    position: absolute;
    top: 0;

    @include media-breakpoint-down(sm) {
      left: 25%;
      right: 25%;
      border-top: 1px solid $onboarding-border;
    }

    @include media-breakpoint-up(sm) {
      bottom: 50%;
      border-left: 1px solid $onboarding-border;
    }
  }

  // Logo
  .m-a-logo-section {
    width: 100%;
    background: $brand-light;
    border-bottom: 1px solid $onboarding-border;

    a {
      text-decoration: none;
    }

    .m-a-logo-section-login{
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);

      a{
        text-decoration: underline;
      }
    }
  }

  .onboading-question {
    @extend .onboarding-font-4;
    @include media-breakpoint-down(sm) {
      @include font(20px, 28px, 0);
    }
    color: $onboarding-text;
    opacity: 0;
  }

  .onboarding-animate-toast, .onboarding-animate-question, .onboarding-animate-options{
    animation: question-ease 0.5s ease;
    animation-timing-function: linear;
  }

  .onboarding-options-section{
    opacity: 0;
  }

  .onboarding-toast{
    opacity: 0;
  }

  .onboarding-options {
    width: max-content;
    width: -moz-max-content;

    .onboarding-option {
      cursor: pointer;
      border: solid 1px $grey-7;
      border-radius: 4px;
      box-shadow: 0 4px 8px 0 rgba(74, 74, 74, 0.1), 0 0 1px 0 rgba(74, 74, 74, 0.15);
    }
  }

  .onboarding-option-help-text {
    color: $grey-2;
  }

  .react-datepicker-wrapper {
    width: 38%;

    @include media-breakpoint-between(md, lg) {
      width: 60%;
    }

    @include media-breakpoint-down(sm) {
      width: 85%;
    }
  }

  .onboarding-date-input-container {
    position: relative;
    display: initial;
    height: 60px;
    cursor: text !important;

    &:hover {
      .onboarding-date-icon, .onboarding-input {
        border-color: $grey-1;
      }
    }

    .onboarding-input {
      height: inherit;
      width: 100% !important;
    }

    .onboarding-date-icon {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 0;
      background-image: url($icon-calendar);
      background-color: #F2F3F3;
      background-size: 24px;
      background-repeat: no-repeat;
      background-position: center;
      height: 60px;
      width: 59px;
      border: solid 1px $grey-5;
      border-left: none;
      border-bottom-right-radius: 4px;
      border-top-right-radius: 4px;
    }
  }

  .onboarding-input {
    resize: none;
    border-radius: 4px;
    border: solid 1px $grey-5;
    outline: none;
    line-height: inherit;

    &::placeholder {
      color: $grey-3
    }

    &:hover,
    &:focus,
    &:active {
      border: solid 1px $grey-1;
    }
  }

  .onboarding-next-button {
    width: max-content;
    height: 60px;
    color: $onboarding-button-text;
    border-radius: 4px;
    background-color: $onboarding-button-background;

    &:disabled,
    &[disabled] {
      background-color: #F2F3F3 !important;
      border: 1px solid $onboarding-border;
      border-radius: 4px;
    }
  }

  .onboarding-required-field {
    color: $onboarding-text;

    &:after {
      content: "*";
      margin-left: 1px;
    }
  }

  .onboarding-link-text {
    &, &:hover, &:active, &:visited {
      color: $onboarding-link !important;
    }
  }

  .onboarding-input-error {
    border: solid 1px $input-required;
  }

  .onboarding-error-message {
    color: $input-required
  }


  // Progress section
  .onboarding-progress-section {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100vw;
    background-color: #ffffff;
    box-shadow: 0 1px 5px 0 rgba(21, 21, 21, 0.15);
  }
}

.onboarding-toast-container {
  width: fit-content;
  width: -moz-fit-content;
  max-width: 527px;
  margin: 22px;

  .onboarding-toast {
    &.Toastify__toast--info {
      background-color: $brand-main;
      color: $grey-1
    }
  }
}

// Animations
@keyframes question-ease {
  from {opacity: 0;}
  to {opacity: 1;}
}
