@import "./cdn-assets-links.scss";

$font_regular: "Freight-Micro-Book",
sans-serif;
$font_book: "Gotham-Book",
sans-serif;
$font_book_medium: "Gotham-Medium",
sans-serif;

@import "./mixins.scss";
@import "../scss/breakpoints.scss";


@font-face {
  font-family: "freightregular";
  src: url($cdn-freight-micro-book);
  font-display: swap;
}

@font-face {
  font-family: "Gotham-Book";
  src: url($cdn-gotham-book);
  font-display: swap;
}

@font-face {
  font-family: "Gotham-Medium";
  src: url($cdn-gotham-book);
  font-display: swap;
}

.font-heading {
  font-family: "freightregular", sans-serif;
}

.font-body {
  font-family: "Gotham-Book", sans-serif;
}

.font-medium {
  font-family: "Gotham-Book", sans-serif;
  font-weight: 700;
}

@for $var from 1 through 6 {

  h#{$var},
  .h#{$var} {
    @extend .font-heading;
    margin-bottom: 0;
  }
}

html,
body {
  @extend .font-body;
}

p {
  margin-bottom: 0;
}

h1,
.h1 {
  @include font(32px, 36px, 0.85px);

  @include media-breakpoint-down(sm) {
    @include font(20px, 24px, 0.5px);
  }
}

h2,
.h2 {
  @include font(24px, 32px, 0.5px);

  @include media-breakpoint-down(sm) {
    @include font(18px, 24px, 0.5px);
  }
}

h3,
.h3 {
  // @include font(25px, 27px); // GIVEN in font hierarchy(MOBILE) but NOT USED in design
  @include font(20px, 24px, 0.5px); // NOT GIVEN in font hierarchy(MOBILE) but USED in designs

  @include media-breakpoint-down(sm) {
    @include font(15px, 24px, 0.5px);
  }
}

h4,
.h4 {
  @extend .font-body;
  @include font(18px, 28px);

  @include media-breakpoint-down(sm) {
    @include font(15px, 24px);
  }
}

h5,
.h5 {
  @extend .font-body;
  @include font(18px, 28px);

  @include media-breakpoint-down(sm) {
    @include font(13px, 16px);
  }
}

.text-body,
p {
  @extend .font-body;
  @include font(15px, 24px);

  @include media-breakpoint-down(sm) {
    @include font(13px, 16px);
  }
}

.text-sm,
.text-sm p {
  @include font(13px, 16px);

  @include media-breakpoint-down(sm) {
    @include font(13px, 16px);
  }
}