@import 'src/assets/scss/colors.scss';
@import 'src/assets/scss/mixins.scss';
@import 'src/assets/scss/custom-variables.scss';
@import 'src/assets/scss/cdn-assets-links.scss';

.personalise-checklist {
  .personalise-checklist-header{
    background-color: $icon-light-info;
    width: 100%;
    z-index: 8;
  }

  .checklist-title {
    color: $grey-0;
    border-bottom: 4px solid $grey-7;
    text-transform: uppercase;
  }

  .personalise-header-link{
    color: $text-link;
    cursor: pointer;
  }

  .done-btn{
    color: $grey-1;
  }

  .checklist-card {
    border: 1px solid $grey-5;
    background-color: $brand-light;
    border-radius: 6px;
    height: 116px;

    .checklist-add-btn {
      height: 32px;
      width: 32px;
      border-radius: 50px;
      background-color: $grey-6;
      font-size: 12px;
      color: $grey-3;
      outline: none;
      border-width: 0px;
    }

    .checklist-action-btn{
      height: 32px;
    }

    .checklist-icon-plus {
      background-image: url($cdn-icon-add-svg);
      background-repeat: no-repeat;
      background-size: 12px;
      background-position: center;
      background-color: $brand-main;
    }

    .item-count-label {
      border-radius: 4px;
      padding: 3px;
      color: $grey-0;
      background-color: $grey-6;
    }
  }

  .add-checklist-bg {
    background-color: $grey-7;
  }
}