@import "../../../../assets/scss/colors.scss";
@import "../../../../assets/scss/breakpoints.scss";
@import "../../../../assets/scss/cdn-assets-links.scss";
@import "../../../../assets/scss/typography.scss";

.accordion-list {
  width: 100%;

  .card {
    border: none;
  }

  .card-header {
    background: unset;
    border: none;
    padding: 0;
    margin: 0;
    cursor: pointer;
  }

  .card-body {
    padding: 0;
    color: #4a4a4a;
  }

  .card-body-contain {
    border-bottom: 1px solid $grey-5;
    cursor: pointer;

    .checklist-circular-progress{
      height: 30px;
      width: 30px;
    }

    &:hover {
      font-weight: 700;
    }

    .progess-divison{
      color: $grey-2;
    }
  }

  .sub-product-header {
    border-bottom: 1px solid $grey-5;
  }

  .card-sub-category{
    border-bottom: 1px solid$grey-5;
    cursor: pointer;

    .progess-divison{
      color: $grey-2;
    }

    &:hover {
      font-weight: 700;
    }
  }

  .sub-product-list {
    cursor: pointer;

    &:hover {
      font-weight: 700;
    }
  }

  .sub-product-list:not(:first-child) {
    border-top: 1px solid $grey-5;
  }

  .card-progress {
    font-size: 13px;
    line-height: 1.54;
    color: #74787b;
  }

  .icon-done {
    height: 20px;
    width: 20px;
    background-image: url($icon-check-svg);
  }

  .icon-checklist {
    .checklist-icon-arrow {
      height: 20px;
      width: 20px;
      background: #ffffff
        url($icon-chevron-right-svg)
        no-repeat center;
      -webkit-transition-duration: 0.4s;
      -moz-transition-duration: 0.4s;
      -o-transition-duration: 0.4s;
      transition-duration: 0.4s;
      -webkit-transition-property: -webkit-transform;
      -moz-transition-property: -moz-transform;
      -o-transition-property: -o-transform;
      transition-property: transform;
      transform: rotate(0deg);
      -webkit-transform: rotate(0deg);
      -ms-transform: rotate(0deg);
    }

    .opened {
      transform: rotate(90deg);
      -webkit-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
    }
  }
}

.check-list-header {
  .checklist-circle-progress {
    height: 25px;
    width: 25px;
  }

  .checklist-title {
    font-weight: 700;
  }

  .checklist-personalize {
    font-weight: 700;
    color: $text-link;
    cursor: pointer;
    display: inline-block;
  }
}

.checklist-progress-count{
  color: $grey-2;
}

.check-list-background {
  position: relative;
  width: 100%;
  border-radius: 4px;
  height: 8px;
  background-color: $grey-7;
  @include media-breakpoint-down(md) {
    height: 4px;
  }
}

.check-list-progress {
  position: absolute;
  top: 0;
  height: 8px;
  border-radius: 4px;
  background-color: $brand-main;
  @include media-breakpoint-down(md) {
    height: 4px;
  }
}

.check-list-hearder-background {
  position: relative;
  width: 100%;
  height: 1px;
  background-color: $grey-5;
}

.check-list-hearder-progress {
  position: absolute;
  top: 0;
  height: 2px;
  background-color: $brand-main;
}

.accordion > .card > .card-header {
  border-radius: 0;
  margin-bottom: 1px !important;
}

.others-cat{
  cursor: pointer;
}

.active-checklist-item {
  @extend .font-medium;
}
