@import '../../../../assets/scss/colors.scss';
@import '../../../../assets/scss/mixins.scss';
@import '../../../../assets/scss/cdn-assets-links.scss';


.product-details-model {

  .product-details-must-have-btn {
    border: none;
    outline: none !important;
    border-radius: 4px;
    border: 0.8px solid $icon-error;
    background-color: $icon-light-error;

    .product-details-must-have-text {
      line-height: 2;
		}
		
		.heart-fill {
			cursor: none;
			position: initial;
			top: 11px;
			right: 0;
			font-size: 20px;
			color: transparent; 
			background-color: transparent;
			border-radius: 4px;
		}

  }

  .product-details-image {
    position: relative;

    .product-details-have {
      position: absolute;
      top: 6px;
    }

    .product-img {
      width: auto;
      height: 173px;
    }
	}
	
	.product-detail-link {
		color: $text-link;
		cursor: pointer;
	}

	.product-note {
    color: $grey-1;
    word-break: break-all;
	}

	.guest-toggle-pills {
    border-radius: 4px;
    padding-top: 2px;
    padding-bottom: 2px;
    border: solid 1px transparent;
    background-color: $icon-light-info;

    i {
			position: inherit;
			background-color: $icon-light-info;
			top: 0;
      color: $grey-4;
      font-size: 12px;
    }

    .guest-icon-active {
      background-image: url($cdn-icon-group-gift-selected-svg);
      background-size: 12px;
      height: 13px;
      width: 13px;
      background-repeat: no-repeat;
      background-position: center;
    }

    .guest-icon-text {
      color: $grey-0 !important;
      @include font(11px, 16px)
    }

    .received-amount {
      color: $grey-0 !important;
    }

    .total-amount {
      color: $grey-1 !important;
    }
  }

  .added-card-no-product-image{
    // TODO: Change this default image if necessary
    @include no-product-image(150px, 100%, 75px)
  }
}