@import '../../../../assets/scss/colors.scss';
@import '../../../../assets/scss/breakpoints.scss';

// CUSTOM CHECKBOX START
.checkbox {
  display: flex;
  align-items: center;
  font-size: 13px;
  cursor: pointer;

  .checkbox__input {
    display: grid;
    grid-template-areas: "checkbox";

    > * {
      grid-area: checkbox;
    }

    input {
      opacity: 0;
      width: 15px;
      height: 15px;
      cursor: pointer;

      &:checked + .checkbox__control {
        background-color: $grey-1;

        i {
          transform: scale(1);
          color: white;
        }
      }
    }

    .checkbox__control {
      display: inline-grid;
      width: 15px;
      height: 15px;
      border-radius: 0.25em;
      border: 0.1em solid $grey-3;

      i {
        transform: scale(0);
      }
    }

  }

  .radio__label{
    line-height: 1;
    @include media-breakpoint-down(lg) {
      line-height: 1.2;
    }
  }

}

// CUSTOM CHECKBOX ENDS