@import "src/assets/scss/colors.scss";

.track-gifts-add-gift-container {

  //Button
  .track-gifts-add-gift-button {
    color: $grey-1 !important;
  }
}

// Input Container
.track-gifts-add-gift-form {
  .track-gifts-add-gift-input-container {

    // Label
    .track-gifts-add-gift-input-label {
      color: $grey-1;

      &.track-gifts-add-gift-required {
        &:after {
          content: "*";
          margin-left: 3px;
          color: $input-required
        }
      }
    }

    // Input
    .track-gifts-add-gift-input {
      border-radius: 4px;
      border: solid 1px $grey-5;
      background-color: $brand-light;
      outline: none;

      &::placeholder {
        color: $grey-4;
      }

      &:active, &:focus {
        border-color: black;
      }

      &.track-gifts-add-gift-input-error{
        border-color: $input-required;
      }
    }

    .track-gifts-add-gift-input-error-text{
      color: $input-required;
    }
  }
}

