@import "src/assets/scss/colors.scss";
@import "src/assets/scss/mixins.scss";

.baby-registry-import-registry {
  .baby-registry-required-field {
    &:after {
      content: "*";
      color: $input-required;
    }
  }

  .import-registry-input-field {
    border-radius: 4px;
    //position: relative;
    border: solid 1px $grey-5;
    background-color: $grey-7;
    outline: none;
    line-height: inherit;

    &.import-registry-error-field {
      border-color: $input-required;
      background: $brand-light;
    }
  }

  .import-registry-submit-url-section, .import-registry-change-link {
    position: absolute;
    right: 18px;
    top: 50%;
    line-height: 1;
    z-index: 10;
    @include prefix(transform, translateY(-50%), (ms, webkit));
  }

  .import-registry-submit-url-section{
    button {
      outline: none !important;
      border: none !important;
      cursor: pointer;
    }
  }

  .import-registry-category-link {
    display: inline-table;
    &,
    &:hover,
    &:visited {
      color: $text-link
    }
  }

  .import-registry-input-error-message {
    color: $input-required !important;
  }

  .baby-registry-import-section {
    max-height: 50vh;
    overflow-y: auto;

    .import-registry-main-content {
      border-radius: 4px;
      border: solid 1px $grey-5;
    }

    // TODO: Make dynamic
    .import-registry-no-content-text {
      color: $grey-1;
      max-width: 578px;
    }

    // TODO: Make dynamic
    .import-registry-loading-text {
      color: $grey-1;
      max-width: 260px;
    }

    .import-registry-spinner {
      border: .2em solid $grey-5;
      border-right-color: $grey-0;
    }

    .import-registry-products {

      .import-registry-products-column:not(:last-child) {
        border-bottom: solid 1px $grey-5;
      }

      .import-registry-item-delete-btn{
        border: none;
        outline: none;
        margin-top: 2px;
        background-color: transparent;
        font-size: 15px;
        border-radius: 3px;

        i{
          color: $grey-4;
        }

        &:hover{
          background-color: $grey-7;
        }

        .import-registry-delete-popover{
          color: $grey-1;
          width: unset;
        }
      }
    }

    .import-registry-grey-text{
      color: $grey-1;
    }
  }
}