@import '../../../../assets/scss/colors.scss';
@import "../../../../assets/scss/cdn-assets-links.scss";

.guest-sort-section {
  .guest-layout-icon-btn {
    border: none;
    background: none;
    outline: none;
    color: $brand-light;
    border-width: 0;
    opacity: 0.5;
    cursor: pointer;
  }

  .icon-list {
    height: 36px;
    width: 36px;
    outline: none;

    border: 1.5px solid $grey-5;
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;

    background-image: url($icon-list);
    background-repeat: no-repeat;
    background-size: 20px;
    background-position: center;

    &:hover,
    &:active {
      background-color: $grey-5;
    }
  }

  .icon-tiles {
    height: 36px;
    width: 36px;
    outline: none;

    border: 1.5px solid $grey-5;
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
    border-left: none;

    background-image: url($icon-tiles);
    background-repeat: no-repeat;
    background-size: 20px;
    background-position: center;

    &:hover,
    &:active {
      background-color: $grey-6;
    }
  }

  .active-layout-guest {
    box-shadow: inset 0 -2px 0 0 $brand-main;
    opacity: 1;
  }
}