@import "src/assets/scss/colors.scss";

.section-title-share-registry {
  color: $grey-1 !important;
}

.social-btn{
  cursor: pointer;
  height: 36px;
}

.visibility-status-btn{
  width: 120px;
  height: 40px;
  border-radius: 50px;
  outline: none;
  border: none;
  position: relative;
  background-color: $icon-success;
  cursor: pointer;

  .registry-status-text{
    position: absolute;
    left: 20px;
    top: 50%;
    transform: translateY(-50%);
    z-index: 2;
  }

  .registry-status-dot{
    position: absolute;
    right: 6px;
    top: 50%;
    height: 30px;
    width: 30px;
    border-radius: 50%;
    background-color: $brand-light;
    transform: translateY(-50%);
    transition: all .2s linear;
    z-index: 5;
  }

  &.disabled-registry-status{
    opacity: 0.7;
  }

  &.private-registry-status{
    background-color: #fa9993;

    .registry-status-text{
      left: unset;
      right: 20px;
    }

    .registry-status-dot{
      right: 100%;
      transform: translateX(calc(100% + 6px)) translateY(-50%);
    }
  }
}

.registry-url-section {
  border: solid 1px transparent;
  border-radius: 4px;

  input {
    border: solid 1px $grey-5;
    outline: none;
  }

  &.focused-input-container {
    border-color: $grey-0;
  }

  &.error-input-container {
    border-color: $text-error;
  }

  .registry-url-input {
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
  }

  .disabled-input-share-registry {
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
    background-color: $grey-7;
    border-right: none;
  }
}

.input-error-message {
  color: $text-error;
}

.share-registry-button-section {
  max-width: max-content !important;
}

.registry-status-bank-detail-section{
  background-color: $grey-7;
  border-radius: 8px;

  .registry-status-bank-link{
    &,&:hover, &:active, &:visited{
      color: $text-link
    }
  }
}