@import '../../../../assets/scss/colors.scss';

.card-list-item {
  list-style-type: none;
  &:last-child {
    border-bottom: 2px solid $grey-7;
  }
}

.drag-add-card {
  .added-card {
    border-radius: 4px;
    border: 1px solid $grey-1;
  }
}

.empty-filter {
  // min-height: 100%;
  border-radius: 4px;
  border: 1px solid $grey-5;

  .empty-filter-title {
    color: $grey-1 !important;
  }
}