@import "../../../src/assets/scss/custom-variables.scss";
@import "../../../src/assets/scss/colors.scss";
@import "src/assets/scss/mixins.scss";
@import "../../assets/scss/breakpoints.scss";

.registry-hero {
  box-shadow: 0 0 1px 0 rgba(74, 74, 74, 0.1);
  background-color: #e4f6f6;

  &.fixed-registry-hero{
    position: fixed;
    top: 0;
    z-index: 15;
  }

  .registry-hero-header {
    margin-bottom: 54.1px;

    .registry-hero-subtitle {
      font-size: 12px;
      line-height: 1.56;
    }
  }

  .registry-due-date-text {
    @include media-breakpoint-down(md) {
      display: none;
    }
  }

  .registry-hero-navigation-links {
    overflow-x: auto;
    white-space: nowrap;

    /* Hide scrollbar for Chrome, Safari and Opera */
    &::-webkit-scrollbar {
      display: none;
    }

    /* Hide scrollbar for IE, Edge and Firefox */
    & {
      -ms-overflow-style: none; /* IE and Edge */
      scrollbar-width: none; /* Firefox */
    }

    .registry-header-link {
      cursor: pointer;
      color: $grey-0;
      text-decoration: none;
      margin-right: (5 * $spacer);
      padding-bottom: (2 * $spacer);

      @include media-breakpoint-down(sm) {
        margin-right: (3 * $spacer);
        padding-bottom: (1 * $spacer);
      }

      h4 {
        @include font(15px, 24px);
        @include media-breakpoint-down(sm) {
          @include font(13px, 24px);
        }
      }

      &:hover,
      &.active {
        border-bottom: solid 1px #212b36;

        h4 {
          font-weight: 700;
        }
      }
    }
  }
  .registry-name-header {
    @include media-breakpoint-down(sm) {
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
}
