@import "src/assets/scss/colors.scss";
@import "src/assets/scss/mixins.scss";
@import "src/assets/scss/breakpoints.scss";

.baby-registry-question-option {
  cursor: pointer;
  display: inline-table;
  border: 1px solid $onboarding-border;
  border-radius: 5px;
  background-color: $brand-light;
  color: $onboarding-text;
  @include font(16px, 20.8px, 0.23px);

  @include media-breakpoint-up(sm) {
    &:hover {
      margin: -1px;
      border-width: 2px;
      border-color: $onboarding-option-border-active;
    }
  }

  &:active, &.onboarding-selected {
    background-color: $onboarding-option-active-background;
    border-width: 2px;
    border-color: $onboarding-option-border-active;
  }
}