@import "src/assets/scss/colors.scss";
@import "src/assets/scss/mixins.scss";
@import "../../../../../assets/scss/breakpoints.scss";
@import "../../../../../assets/scss/cdn-assets-links.scss";

.baby-registry-cash-fund-card {
  border-radius: 8px;
  border: solid 1px $grey-5;
  background-color: $brand-light;

  .cash-fund-image {
    width: 100%;
    object-fit: cover;
    object-position: center;
    -o-object-position: center;
    -o-object-fit: cover;
    max-height: 76px;
    border-radius: 6px;
    border: solid 1px $grey-5;
  }

  .cash-fund-description {
    color: $grey-1;
    max-width: 383px;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 2;
    display: -webkit-box;
    -webkit-box-orient: vertical;
  }

  .cash-fund-add-btn {
    height: 44px;
    width: 44px;
    border-radius: 50px;
    background-color: #afe3e4;
    outline: none;
    border-width: 0;

    background-image: url($cdn-icon-add-svg);
    background-repeat: no-repeat;
    background-size: 20px;
    background-position: center;

    &:hover,
    &:focus,
    &:active {
      background-color: $btn-primary-click;
    }
  }

  .baby-registry-cash-fund-no-image {
    @include no-product-image(84px, 100%, 42px);

     // Custom added not present in design
    @include media-breakpoint-down(sm) {
      @include no-product-image(84px, 50%, 42px);
    }
  }
}
