@import "../../../../assets/scss/colors.scss";
@import "../../../../assets/scss/breakpoints.scss";

.personalise-footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  border-top: 1px solid $grey-5;
  background-color: $brand-light;
  z-index: 99999999;

  .personalise-progress-background {
    position: relative;
    width: 100%;
    border-radius: 50px;
    height: 6px;
    background-color: $grey-7;
  }

  .personalise-progress {
    position: absolute;
    top: 0;
    height: 6px;
    border-radius: 50px;
    background-color: $brand-main;
  }

  .personalise-footer-icon{
    color: $grey-2;
    border-radius: 4px;
    font-size: 24px;
    cursor: pointer;
    background-color: $brand-light;
    border: 1px solid $onboarding-border;

    @include media-breakpoint-down(sm){
      font-size: 16px;
    }


    &.icon-chevron-left{
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
      border-right-width: 0.5px;
    }

    &.icon-chevron-right{
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
      border-left-width: 0.5px;

    }

    &:hover, &:active{
      border-color: $onboarding-option-border-active;
      border-width: 2px;
    }
  }

  .footer-skip-question-link{
    position: absolute;
    top: -55px;
    left: 50%;
    transform: translateX(-50%);
  }
}