@import "../../../../assets/scss/colors.scss";

.view-filter {

  .view-filter-heading {
    border-bottom: 1px solid $grey-5;
    color: $grey-0;

  }

  .filter-clear-all {
    color: $text-link;
    cursor: pointer;
  }

  .filter-title {
    color: $grey-0 !important;
  }

  .filter-item {
    border-bottom: 1px solid $grey-5;
  }

  .fliter-checkbox-section {

    .view-fliter-checkbox {
      display: flex;
      align-items: center;
      font-size: 11px;
      font-weight: unset;

      .radio__label {
        color: #3c4049;
        line-height: 1;
      }

      .checkbox__input {
        display: grid;
        grid-template-areas: "checkbox";
        color: #3c4049;

        >* {
          grid-area: checkbox;
        }

        input {
          opacity: 0;
          width: 15px;
          height: 15px;

          &:checked+.checkbox__control {
            background-color: $grey-1;

            i {
              transform: scale(1);
              color: $brand-light;
            }
          }
        }

        .checkbox__control {
          display: inline-grid;
          width: 15px;
          height: 15px;
          border-radius: 0.25em;
          border: 0.1em solid $grey-3;

          i {
            transform: scale(0);
          }
        }
      }
    }
  }
}