@import "src/assets/scss/mixins.scss";
@import "src/assets/scss/colors.scss";
@import "src/assets/scss/cdn-assets-links.scss";

.find-registry {
  .find-registry-header {
    @include font(40px, 48px, 0);
    color: $grey-0;
  }

  .find-registry-content {
    color: $grey-1;
  }

  .find-registry-displayName {
    color: $grey-0;
  }

  .find-registry-card {
    border-top: 1px solid $grey-6;
    border-bottom: 1px solid $grey-6;
    cursor: pointer;

    .find-registry-action-btn {
      border: none;
      outline: none;
      border-radius: 50px;
      width: 45px;
      height: 45px;
      background-color: $grey-7;
      background-image: url($icon-arrow-right-svg);
      background-repeat: no-repeat;
      background-size: 44px;
    }

    &:hover {
      background-color: $grey-7;

      .find-registry-action-btn {
        background-image: url($icon-arrow-right-hover-svg);
      }
    }
  }

  .find-registry-help-primary-btn {
    color: $grey-1 !important;
    background-color: $brand-main;
    text-decoration: none;
    cursor: pointer;

    &:hover {
      color: $grey-0 !important;
      background-color: $tiffany-blue;
    }
  }

  .find-registry-email-link {

    &,
    &:hover,
    &:visited {
      color: $text-link
    }
  }

  .find-registry-help-disable-btn {
    color: $grey-1 !important;
    background-color: $grey-7;
    text-decoration: none;
    cursor: pointer;

    &:hover {
      color: $grey-0 !important;
      background-color: $grey-5;
    }
  }

  .find-registry-search-container {
    position: relative;
    width: 100%;

    .find-registry-search-input {
      border-radius: 2px;
      //TODO: Update to global color
      border: solid 1px #dfe1e7;
      background-color: $brand-light;
      outline: none;


      &::placeholder,
      &::-ms-input-placeholder,
      &:-ms-input-placeholder {
        color: $grey-3;
        opacity: 1;
        /* Firefox */
      }

      &:focus,
      &:active {
        border-color: $grey-0
      }
    }

    .find-registry-search-icon {
      position: absolute;
      top: 50%;
      left: 0;
      margin: 0 12px;
      //TODO: Update to global color
      color: #989fae;
      cursor: pointer;
      @include prefix(transform, translateY(-50%), (ms, webkit));
    }

    .find-registry-search-input-link {
      position: absolute;
      top: 50%;
      right: 12px;
      cursor: pointer;
      @include prefix(transform, translateY(-50%), (ms, webkit));

      &,
      &:hover,
      &:active,
      &:visited {
        color: $text-link !important;
      }
    }
  }
}