@import "../../../../assets/scss/colors.scss";
@import "../../../../assets/scss/breakpoints.scss";

.modal-backdrop {
  width: auto !important;
  right: 0;
}
.m-a-modal {
  .modal-content {
    margin: 0px 30px;
    width: auto;

    @include media-breakpoint-down(sm) {
      height: 100vh;
      height: -webkit-fill-available;
      margin: 0;
      overflow: scroll;
    }
  }

  @include media-breakpoint-down(sm) {
    .modal-dialog {
      width: 100vw;
      margin: 0;
    }

    .modal-body {
      overflow-y: scroll;
      &::-webkit-scrollbar {
        display: none;
      }
    }
  }

  .modal-xl {
    max-width: 898px;
  }

  .modal-md {
    max-width: 596px;
    @include media-breakpoint-down(md) {
      max-width: 100vw ;
    }
  }
}

.m-a-modal-close {
  cursor: pointer;
  color: $grey-4;
}

.m-a-modal-content {
  * {
    &::-webkit-scrollbar-track {
      background-color: transparent;
    }

    &::-webkit-scrollbar {
      width: 12px;
      background-color: transparent;
    }

    &::-webkit-scrollbar-thumb {
      border-radius: 50px;
      background-color: $grey-5;
    }
  }
}
