@import "src/assets/scss/colors.scss";
@import "src/assets/scss/mixins.scss";

.cash-fund-modal-content {
  height: 60vh;
  overflow-y: auto;

  .asterik {
    font-size: 13px;
    color: #d10e00;
  }

  .cash-fund-modal-image-section {
    position: relative;

    .cash-fund-modal-image {
      width: 100%;
      height: auto;
      border-radius: 6px;
      border: solid 1px $grey-5;
    }

    .change-cash-fund-image-icon {
      position: absolute;
      top: 12px;
      right: 12px;
      cursor: pointer;
      font-size: 20px;
      color: $grey-4;
      background-color: $grey-6;
      border-radius: 4px;
    }

    .cash-fund-modal-no-image {
      @include no-product-image(250px, 100%, 70px);
    }
  }

  .cash-fund-input-section {
    border-left: 1px solid $grey-5;

    .cash-fund-input-container {
      .cash-fund-input-label {
        color: $grey-1;
      }

      .cash-fund-textarea {
        height: unset;
      }

      .cash-fund-input-error-text{
        color: $input-required;
      }
      .cash-fund-input,
      .cash-fund-textarea {
        border-radius: 4px;
        border: solid 1px $grey-5;
        outline: none;
        line-height: inherit;
        position: relative;

        &::placeholder {
          color: $grey-3;
        }

        &:hover,
        &:focus,
        &:active {
          border: solid 1px $grey-1;
        }

        &::-webkit-outer-spin-button,
        &::-webkit-inner-spin-button {
          -webkit-appearance: none;
          margin: 0;
        }

        /* Firefox */
        &[type="number"] {
          -moz-appearance: textfield;
        }

        &.cash-fund-input-error{
          border-color: $input-required;
        }

      }

      .cash-fund-price-input-currency {
        position: absolute;
        color: $grey-3 !important;
        top: 50%;
        left: 12px;
        @include prefix(transform, translateY(-50%), (ms, webkit));
      }

      .dd-wrapper {
        width: 100%;
        font-size: 15px;
        max-height: unset;

        .dd-header {
          outline: none;
          border-radius: 4px;
          border: solid 1px $grey-5;

          &:hover, &:active, &:focus {
            border-color: $grey-1;

            & ~ .dd-list{
              border-color: $grey-1;
            }
          }

          .fa {
            font-size: 24px;
            color: $grey-4;
            //border: solid $grey-4;
            //border-width: 0 3px 3px 0;
            //display: inline-block;
            //border-radius: 2px;
            //margin-bottom: 7px;
            //padding: 4px;
            //transform: rotate(45deg);
            //-webkit-transform: rotate(45deg);
          }

        }

        .dd-list {
          border-bottom-left-radius: 4px;
          border-bottom-right-radius: 4px;
          position: absolute;
          left: 0;
          border: none;
          box-shadow: 0 8px 16px 0 rgba(74, 74, 74, 0.1), 0 0 1px 0 rgba(74, 74, 74, 0.15);
          font-weight: normal;
          border-left: solid 1px $grey-5;
          border-bottom: solid 1px $grey-5;
          border-right: solid 1px $grey-5;
          width: 100%;
          margin-top: -2px;
          max-height: 500px;

          .dd-scroll-list {
            max-height: 250px;
            padding: 0;

            .dd-list-item {
              font-size: 15px;
              padding: 8px 20px;
              outline: none;

              span{
                display: none;
              }

              &.selected {
                background-color: $grey-6;
                color: $grey-0;
              }

              &:hover {
                background-color: $grey-6;
                color: $grey-0;
              }
            }
          }
        }
      }

      .cash-fund-select {
        border-radius: 4px;
        outline: none;
        line-height: inherit;
        background-color: #ffffff;

        &.cash-fund-input-error{
          .dd-header{
            border-color: $input-required;
            & ~ .dd-list{
              border-color: $input-required;
            }
          }
        }

      }

    }

    .cash-fund-must-have-selected {
      border: solid 1px $icon-error;
      background-color: $icon-light-error;
    }
  }
}
