@import "../../../../assets/scss/colors.scss";

.filter-section-filter {
  position: relative;

  .filter-icon {
    font-size: 20px;
  }

  .filter-content {
    position: absolute;
    width: max-content;
    right: 0;
    border-radius: 6px;
    border: solid 1px #9fa1a4;
    display: none;
    z-index: 350;
    background-color: white;

    &.show {
      display: block;
    }

    .filter-content-bottom-buttons {
      border-top: solid 1px $grey-5;
      color: $grey-1;

      .filter-clear-button {
        border-radius: 4px;
        border: none;
        outline: none !important;
        background-color: $grey-7;
      }

      .filter-done-button {
        border-radius: 4px;
        border: none;
        outline: none !important;
        background-color: $brand-main;
      }
    }
  }
}