@import "../../../../../assets/scss/colors.scss";
@import "../../../../../assets/scss/common-styles.scss";
@import "../../../../../assets/scss/typography.scss";
@import "../../../../../assets/scss/custom-variables.scss";
@import "../../../../../assets/scss/mixins.scss";
@import "../../../../../assets/scss/breakpoints.scss";

.add-gift-another-store {
  max-height: 56vh;
  overflow-y: auto;

  @include media-breakpoint-down(sm) {
    max-height: 100%;
  }

  @include media-breakpoint-down(sm) {
    max-height: 100%;
  }

  .add-gift-store-input-error {
    border-color: $input-required !important;
  }
  
  .add-gift-store-input-error-text {
    color: $input-required !important;
  }

  .hide-product-view {
    border: 1px solid $grey-5;

    .spinner-border {
      border: .2em solid $grey-5;
      border-right-color: $grey-0;
    }

    .hide-product-details {
      color: $grey-1;
      width: 260px;
    }
  }
}

.add-gift-radio-body{
  font-weight: 400;
}

.add-gift-store-label {
  color: $grey-1;
}

.m-a-input-container {
  input,
  textarea {
    resize: none;
    border-radius: 4px;
    border: solid 1px $grey-5;
    outline: none;
    max-width: 100%;
    line-height: inherit;
    height: unset;

    &::placeholder {
      color: $grey-3
    }

    &:hover,
    &:focus,
    &:active {
      border: solid 1px $grey-1;
    }
  }

  change-link-input {
    position: relative;
  }

  a {
    position: absolute;
    right: 30px;
    top: 50%;
    line-height: 1;
    @include prefix(transform, translateY(-50%), (ms, webkit));
    color: $text-link;

    &:hover,
    &:visited,
    &:active {
      color: $text-link;
    }
  }

  .ma-input-required {
    &:after {
      content: "*";
      margin-left: 3px;
      color: $input-required;
    }
  }

  .input-background {
    background-color: $grey-7;
  }

  .price-input {
    position: relative;

    &::before {
      content: "$";
      position: absolute;
      left: 2 * $spacer;
      @extend .text-body;
      @extend .center-absolute;
      color: $grey-3 !important;
    }
  }

}

.add-gift-image-select {
  border-right: solid 1px $grey-5;
  @include media-breakpoint-down(sm) {
    border-right: none;
  }
  position: relative;

  img {
    max-width: 100%;
  }

  i {
    cursor: pointer;
    position: absolute;
    top: 12px;
    right: 12px;
    font-size: 20px;
    color: $grey-4;
    background-color: $grey-6;
    border-radius: 4px;
  }
}

.active-must-have {
  border: solid 1px $icon-error !important;
  background-color: $icon-light-error !important;
}

.active-group-gift {
  border: solid 1px $icon-info !important;
  background-color: $icon-light-info !important;
}

.active-purchased {
  border: solid 1px $icon-info !important;
  background-color: $icon-light-info !important;
}

.add-gift-link {
  color: $text-link;

  &:hover,
  &:visited,
  &:active {
    color: $text-link;
  }
}

.add-gift-another-store-change-link {
  position: absolute;
  right: 18px !important;
  top: 50%;
  line-height: 1;
  z-index: 10;
  @include prefix(transform, translateY(-50%), (ms, webkit));
}

.add-gift-another-store-submit-url-section {
  position: absolute;
  right: 15px !important;
  top: 50%;
  line-height: 1;
  transform: translateY(-50%);

  button {
    outline: none !important;
    border: none !important;
    cursor: pointer;
  }
}