@import "src/assets/scss/colors.scss";
@import "src/assets/scss/mixins.scss";
@import "src/assets/scss/custom-variables.scss";
@import "../../../../assets/scss/breakpoints.scss";
@import "../../../../assets/scss/cdn-assets-links.scss";

.baby-registry-track-gifts-card {
  border-top: 1px solid $grey-6;

  // Product Image
  .track-gifts-card-image {
    width: 100%;
    height: auto;
    border-radius: 6px;
  }

  .track-gifts-no-product-image {
    @include no-product-image(54px, 100%, 30px);
  }

  .track-gifts-product-description {
    // Title
    .track-gifts-product-title {
      overflow: hidden;
      text-overflow: ellipsis;
      -webkit-line-clamp: 2;
      display: -webkit-box;
      -webkit-box-orient: vertical;
    }

    //  Cash Fund Progress
    .track-gifts-cash-fund-progress-bar {
      border-radius: 50px;
      background-color: $grey-6;
      position: relative;
      height: 4px;
      width: 30%;

      .track-gifts-cash-fund-progress {
        position: absolute;
        top: 0;
        left: 0;
        background-color: $tiffany-blue;
        border-radius: 50px;
        height: inherit;
      }
    }
  }

  //  Gift Message Popover
  .track-gifts-gift-message-container {
    position: relative;
    height: 36px;
    width: 36px;
    background-color: $grey-7;
    background-image: url($gift-message-svg);
    background-repeat: no-repeat;
    background-size: 20px;
    background-position: center;
    border-radius: 2px;
    cursor: pointer;
    outline: none !important;
    border: solid 1px transparent;

    .track-gifts-gift-message-content {
      position: absolute;
      display: none;
      z-index: 13;
      top: 100%;
      right: 0;
      width: 332px;
      border-radius: 10px;
      box-shadow: $mbr-box-shadow-blue;
      border: solid 1px $tiffany-blue;
      background-color: $brand-light;

      .track-gifts-gift-message {
        word-break: break-word;
        overflow-y: auto;
        max-height: 300px;
        color: $grey-1 !important;
      }
    }

    &:hover,
    &:active,
    &:focus,
    .track-gifts-gift-message-content:hover {
      border-color: $tiffany-blue;
      background-color: $brand-main;

      .track-gifts-gift-message-content {
        display: block;

        &:hover {
          background-color: $brand-light;
        }
      }
    }
  }

  // Group Gift user details
  .track-gifts-group-users-details {
    position: relative;

    .track-gifts-view-friends-link {
      border: none;
      outline: none;
      &,
      &:hover,
      &:active {
        color: $text-link;
      }
    }

    .track-gifts-friends-detail {
      position: absolute;
      top: -10px;
      left: -10px;
      display: none;
      box-shadow: $mbr-box-shadow;
      border: solid 1px $grey-5;
      background-color: $brand-light;
      border-radius: 6px;
      z-index: 13;

      &:hover{
        display: block;
      }

      .friends-detail-container {
        border: none !important;
      }

      .friends-detail-header {
        border-bottom: 2px solid $grey-6;

        .close-friends-detail-icon {
          font-size: 14px;
          color: $grey-4;
          cursor: pointer;
        }
      }

      .friend-detail {
        &:not(:last-child) {
          border-bottom: 2px solid $grey-6;

          .friend-user-email {
            color: $grey-1;
          }
        }

        .friend-contribution-amount {
          color: $grey-1;
        }
      }
    }

    .track-gifts-show-friends-detail{
      display: block;
    }
  }

  // Single user detail
  .track-gifts-user-detail {
    .track-gifts-user-email {
      color: $grey-1;
    }

    @include media-breakpoint-up(md) {
      max-width: 50%;
    }
  }

  .track-gifts-purchased-time {
    width: max-content;
    width: -moz-max-content;
    color: $grey-1;
  }

}

.friend-detail-modal-container {
  border-bottom: 1px solid $grey-5;
}

.track-gift-user-info-name, .track-gifts-user-info-email {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}


