@import "../../../../assets/scss/colors.scss";


.view-guest-header {
  background-color: $grey-2;

  .guest-registry-status {
    border-right: 1px solid $grey-7;

    .registry-status-title {
      color: $brand-light;
    }

    .public-status-dot {
      width: 11px;
      height: 11px;
      border-radius: 50%;
      background-color: $icon-success;
    }

    .private-status-dot {
      width: 11px;
      height: 11px;
      border-radius: 50%;
      background-color: #fa9993;
    }
  }

  .view-guest-text {
    color: $brand-light !important;
  }

  .view-guest-link {
    cursor: pointer;
  }
}

.guest-view-registry-header {
  position: relative;
  height: 250px;
  background-size: cover;

  img {
    width: 100%;
    height: inherit;
    object-fit: cover;
  }

  .cover-pic-update-options {
    position: absolute;
    top: 24px;
    width: 100%;

    .update-cover-options {
      background-color: $grey-7;
      border-radius: 3px;
      font-size: 20px;
      border: solid 1px transparent;
    }

    .view-registry-cover-option-active {
      background-color: $brand-light;
      color: $grey-1;
      border: solid 1px $grey-1;
    }
  }

  .header-registry-detail {
    position: absolute;
    bottom: 0;
    width: 100%;

    .guest-registry-header-border {
      background-color: rgba(255, 255, 255, 0.9);
      border-radius: 4px;
      border: 2px solid $grey-6;
    }

    .edit-name-icon {
      cursor: pointer;
      color: $grey-2;
    }

    .view-registry-header-greeting {
      color: $grey-1;
    }
  }

  .add-greeting-link {
    color: $text-link;
    cursor: pointer;
  }

  // .header-buttons {
  //   text-align: right;
  // }
}