@import "../../../../assets/scss/colors.scss";
@import "../../../../assets/scss/breakpoints.scss";

.personalise-header {
  background-color: #ebfefe;
  width: 100%;
  z-index: 8;
  position: relative;

  .m-a-personalise-logo-section {
    width: 100%;
    background: $brand-light;
    border-bottom: 1px solid $onboarding-border;

    a {
      text-decoration: none;
    }
  }

  .personalize-close-btn{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 40px;
    background-color: $brand-light;
    border-radius: 4px;
    border: 1px solid $personalise-exit-btn-border;

    @include media-breakpoint-down(sm){
      right: 20px;
    }

    .personalise-close-btn-text{
      color: $onboarding-text;
      line-height: 1 !important;
    }

    .icon-close{
      font-size: 14px;
      color: $personalise-exit-btn-icon;
    }
  }

  .personalise-text {
    // TODO: Change color after global styling
    color: #121212 !important;
  }

  .personalise-close {
    color: $grey-4;
    cursor: pointer;
  }
}