@import "src/assets/scss/colors.scss";

.guest-address-section {
  border-radius: 4px;
  border: 1px solid $grey-5;
  background-color: $grey-7;
}

.guest-footer-btn {
  text-transform: capitalize;
}

.guest-link {
  color: $text-link;

  &:hover,
  &:visited,
  &:active {
    color: $text-link;
  }
}

.address-copy-link {
  position: inherit !important;
}

.guest-store-name{
  text-transform: capitalize;
}