@import "src/assets/scss/colors.scss";
@import "src/assets/scss/custom-variables.scss";
@import "src/assets/scss/mixins.scss";

.edit-gift-content {

  max-height: 65vh;
  overflow-y: auto;

  .edit-gift-right-border{
    border-right: solid 1px $grey-5;
  }

  .edit-gift-image-section {
    position: relative;

    .edit-gift-image {
      width: 100%;
      height: auto;
      border-radius: 6px;
      border: solid 1px $grey-5;
    }

    .edit-gift-custom-image-more-options {
      position: absolute;
      top: 2 * $spacer;
      right: 2 * $spacer;
    }

    .edit-gift-update-image-option {
      color: $grey-4;
      background-color: $grey-6;
      border-radius: 4px;
      height: 36px;
      width: 36px;
    }

    .edit-gift-default-image {
      height: 233px;
      border-radius: 6px;
      border: solid 1px $grey-5;
    }

    .edit-gift-upload-image-size {
      color: $grey-1;
    }
  }

  .edit-gift-detail {
    .edit-gift-input-container {
      .edit-gift-input-label {
        color: $grey-1
      }

      .edit-gift-input-error{
          border-color: $input-required !important;
      }

      .edit-gift-input-error-text{
        color: $input-required;
      }

      .edit-gift-input {
        border-radius: 4px;
        border: solid 1px $grey-5;
        outline: none;
        line-height: inherit;
        height: unset;

        &::placeholder {
          color: $grey-3
        }

        &:hover,
        &:focus,
        &:active {
          border: solid 1px $grey-1;
        }
      }

      .edit-gift-store-url-submit, .edit-gift-input-change-url {
        position: absolute;
        top: 50%;
        right: 2 * $spacer;
        line-height: 1;
        @include prefix(transform, translateY(-50%), (ms, webkit));
      }

      .edit-gift-store-url-submit {
        button {
          outline: none !important;
          border: none !important;
          cursor: pointer;
        }
      }

      .edit-gift-resize-none {
        resize: none;
      }

      .edit-gift-input-overlay {
        position: relative;

        .edit-gift-price-currency {
          position: absolute;
          left: 2 * $spacer;
          top: 50%;
          @include prefix(transform, translateY(-50%), (ms, webkit));
          color: $grey-3 !important;
        }
      }

      //  Variant designs
      .edit-gift-variant {
        height: 28px;
        width: 28px;
        background-color: #ffffff;
        object-position: center;
        border-radius: 50%;
        outline: none;
        border: 2px solid transparent;
        background-repeat: no-repeat;
        background-position: center;

        &:hover, &.edit-gift-variant-selected {
          border-color: $grey-1;
        }
      }

      .edit-product-variant {
        display: none;
    
        .product-variant-text {
          color: $grey-1;
        }
    
        .product-variant-circle-disk {
          border-radius: 50%;
          border: 1.5px solid transparent;
          // background-color: yellow;
    
          .product-variant-image {
            border-radius: 50%;
            height: 28px;
            width: 28px;
          }
        }
    
        .product-variant-avalible {
          cursor: pointer;
    
          &:hover {
            border: 1.5px solid $grey-0;
          }
        }
    
        .product-variant-disabled {
          position: relative;
          cursor: not-allowed;
    
          &::after {
            position: absolute;
            content: "";
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: url($icon-soldout-variant) no-repeat 50%;
            z-index: 9999;
          }
        }
    
        .active-variant-product {
          border: 1.5px solid $grey-0;
        }
      }
  
    }
    .edit-gift-active-must-have {
      border: solid 1px $icon-error !important;
      background-color: $icon-light-error !important;
    }

    .edit-gift-active-group-gift {
      border: solid 1px $icon-info !important;
      background-color: $icon-light-info !important;
    }

    .edit-gift-active-purchased {
      border: solid 1px $icon-info !important;
      background-color: $icon-light-info !important;
    }
  }

}

.edit-gift-links {
  line-height: 1;
  color: $text-link;

  &:hover,
  &:visited,
  &:active {
    color: $text-link;
  }
}

.edit-gift-learn-more-modal-content {
  .edit-gift-learn-more-modal-text {
    color: $grey-1 !important;
  }
}

.edit-gift-disabled {
  opacity: 0.7;
  pointer-events: none;
}

.edit-gift-disabled {
  opacity: 0.7;
  pointer-events: none;
}