@import "src/assets/scss/colors.scss";
@import "../../../../assets/scss/breakpoints.scss";
@import "../../../../assets/scss/cdn-assets-links.scss";

.baby-registry-todo-widget {

  .baby-registry-todo-section {
    position: relative;

    .baby-steps-button {
      all: unset;
      cursor: pointer;
      outline: none !important;
      border-radius: 4px;
      border: solid 2px transparent;
      //TODO: Change to global color
      background-color: #d7f6f6;

      &:focus, &:hover {
        box-shadow: 0 4px 8px 0 rgba(60, 64, 73, 0.1), 0 0 1px 0 rgba(60, 64, 73, 0.15);
        border: solid 2px $brand-main;
      }

      &:focus {
        & ~ .baby-steps-todo-content {
          display: block;
        }
      }

      .baby-steps-icon {
        &:before {
          background-image: url($baby-steps-svg);
          background-size: 40px;
          background-repeat: no-repeat;
          display: inline-block;
          width: 42px;
          height: 42px;
          content: "";
        }
        @include media-breakpoint-down(sm) {
          &:before {
            background-image: url($baby-steps-svg);
            background-size: 25px;
            background-repeat: no-repeat;
            display: inline-block;
            width: 25px;
            height: 25px;
            content: "";
            margin-top: 4px;
          }
        }
      }

      .baby-steps-text{
        @include media-breakpoint-down(sm) {
          display: none;
        }
      }
    }

    .baby-steps-todo-content {
      display: none;
      position: absolute;
      max-height: 400px;
      overflow-y: auto;
      top: 100%;
      right: 0;
      z-index: 15;
      min-width: 337px;
      box-shadow: 0 8px 16px 0 rgba(60, 64, 73, 0.1), 0 0 1px 0 rgba(60, 64, 73, 0.15);
      //TODO: Change to global color
      border: solid 2px #89d6d7;
      background-color: $brand-light;
      @include media-breakpoint-down(sm) {
        min-width: 250px;
      }

      &:hover {
        display: block;
      }

      .baby-step-todo {
        border-top: solid 1px $grey-6;
        border-bottom: solid 1px $grey-6;

        &:first-child {
          border-top: none;
        }

        &:last-child {
          border-bottom: none;
        }


        .baby-step-action-btn {
          border: none;
          outline: none;
          flex: none;
          border-radius: 50px;
          width: 45px;
          height: 45px;
          background-image: url($icon-arrow-right-svg);
          background-repeat: no-repeat;
          background-size: 44px;
        }
      }
    }
  }

  .baby-registry-no-todo-section {
    .no-todo-icon {
      &:before {
        background-image: url($todo-done-svg);
        background-size: 42px;
        display: inline-block;
        width: 42px;
        height: 42px;
        content: "";
      }
    }
  }
}