@import '../../../../assets/scss/colors.scss';
@import '../../../../assets/scss/mixins.scss';
@import '../../../../assets/scss/cdn-assets-links.scss';


.MA-gift-footer-btn {
  color: $grey-1 !important;
}

.give-gift-model {
  background-color: $brand-light;

  .guest-toggle-pills {
    border-radius: 4px;
    padding-top: 2px;
    padding-bottom: 2px;
    border-radius: 20px;
    border: solid 1px transparent;
    background-color: $icon-light-info;

    i {
      color: $grey-4;
      font-size: 12px;
    }

    .guest-icon-active {
      background-image: url($cdn-icon-group-gift-selected-svg);
      background-size: 12px;
      height: 13px;
      width: 13px;
      background-repeat: no-repeat;
      background-position: center;
    }

    .guest-icon-text {
      color: $grey-0 !important;
      @include font(11px, 16px)
    }

    .received-amount {
      color: $grey-0 !important;
    }

    .total-amount {
      color: $grey-1 !important;
    }
  }

  .group-gift-background {
    position: relative;
    width: 100%;
    border-radius: 50px;
    height: 4px;
    background-color: $grey-7;
  }

  .group-gift-progress {
    position: absolute;
    top: 0;
    height: 4px;
    border-radius: 50px;
    background-color: $brand-main;
  }

}

.give-gift-text-link {
  color: $text-link;
  cursor: pointer;
}