@import '../../../../assets/scss/colors.scss';
@import "../../../../assets/scss/cdn-assets-links.scss";

.counter {
  .icon {
    background-size: 20px 20px;
    background-position: center;
    background-repeat: no-repeat;
    background-color: inherit;
    height: 25px;
    width: 25px;
  }

  .icon-btn {
    border: none;
    background: none;
    outline: none;
    color: $grey-4;
    border-width: 0;
  }

  .counter-text{
    min-width: 36px;
    height: 36px;
    line-height: 1;
    border: 1.5px solid $grey-5;
  }

  .square-minus-icon {
    height: 36px;
    width: 36px;
    background-color: $grey-7;
    outline: none;

    border: 1.5px solid $grey-5; 
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
    border-right: none;   

    background-image: url($icon-minus-light);
    background-repeat: no-repeat;
    background-size: 12.8px;
    background-position: center;
    // background-color: $grey-3;

    &:hover,
    &:active {
      background-color: $grey-5;
    }
  }

  .square-plus-icon {
    height: 36px;
    width: 36px;
    background-color: $grey-7;
    outline: none;

    border: 1.5px solid $grey-5; 
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
    border-left: none;   

    background-image: url($icon-add-light);
    background-repeat: no-repeat;
    background-size: 12.8px;
    background-position: center;
    // background-color: $grey-3;

    &:hover,
    &:active {
      background-color: $grey-5;
    }
  }
}