@import "src/assets/scss/colors.scss";

.questionnaire-progress-bar{

  .questionnaire-progress-text{
    color: $onboarding-progress-text;
  }

  .questionnaire-progress-bar-container{
    position: relative;
    height: 15px;
    border-radius: 7.5px;
    background-color: $onboarding-progress-bar-background;

    .questionnaire-progress{
      position: absolute;
      top: 0;
      left: 0;
      height: inherit;
      border-radius: inherit;
      background-color: $onboarding-button-background;
    }
  }
}