@import '../../../../assets/scss/colors.scss';

// CUSTOM RADIO BUTTON START
.radio {
  cursor: pointer;

  .radio__input {
    display: flex;

    input {
      opacity: 0;
      width: 0;
      height: 0;

      &:checked+.radio__control {
        // background: radial-gradient(currentcolor 50%, rgba(255, 0, 0, 0) 51%);
        background: radial-gradient(black 30%, rgba(255, 0, 0, 0) 36%);
        border-color: black;
        background-position: center;
      }

    }

    .radio__control {
      display: block;
      width: 20px;
      height: 20px;
      border-radius: 50%;
      border: 0.1em solid $grey-3;
      transform: translateY(-0.05em);
    }

  }
}