@import "icon-variables";

@font-face {
  font-family: "#{$icon-font-family}";
  src: url("#{$icon-font-path}/#{$icon-font-family}.eot?72nh2l");
  src: url("#{$icon-font-path}/#{$icon-font-family}.eot?72nh2l#iefix")
      format("embedded-opentype"),
    url("#{$icon-font-path}/#{$icon-font-family}.ttf?72nh2l") format("truetype"),
    url("#{$icon-font-path}/#{$icon-font-family}.woff?72nh2l") format("woff"),
    url("#{$icon-font-path}/#{$icon-font-family}.svg?72nh2l##{$icon-font-family}")
      format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "#{$icon-font-family}" !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

