@import "mixins.scss";
@import "colors.scss";
@import "custom-variables.scss";
@import "cdn-assets-links.scss";
@import "breakpoints.scss";

.center-absolute {
  top: 50%;
  @include prefix(transform, translateY(-50%), (ms, webkit))
}

.m-a-popover {
  position: relative;

  .m-a-popover-content {
    visibility: hidden;
    position: absolute;
    text-align: left;
    width: 276px;
    box-shadow: $mbr-box-shadow;
    background: $brand-light;
    border: $grey-5 solid 1px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    border-radius: 4px;

    .m-a-popover-content-desc, .m-a-popover-content-title {
      color: $grey-1
    }

    &:after {
      content: "";
      position: absolute;
      width: 0;
      height: 0;
    }
  }

  //     Positions of popover
  &.m-a-popover-right {
    .m-a-popover-content {
      top: 0;
      left: 100%;
      margin-left: 11px;

      &:after {
        top: 5%;
        right: 100%;
        border-top: 11px solid transparent;
        border-bottom: 11px solid transparent;
        border-right: 11px solid $brand-light;
        -webkit-filter: drop-shadow(-2px 0.5px $grey-5);
      }
    }
  }

  &.m-a-popover-left {
    .m-a-popover-content {
      top: 0;
      right: 100%;
      margin-right: 11px;

      &:after {
        top: 5%;
        left: 100%;
        border-top: 11px solid transparent;
        border-bottom: 11px solid transparent;
        border-left: 11px solid $brand-light;
        -webkit-filter: drop-shadow(2px 0.5px $grey-5);
      }
    }
  }

  &.m-a-popover-top {
    .m-a-popover-content {
      bottom: 100%;
      left: 50%;
      transform: translateX(-50%);
      margin-bottom: 11px;

      &:after {
        top: 100%;
        left: 50%;
        transform: translateX(-50%);
        border-left: 11px solid transparent;
        border-right: 11px solid transparent;
        border-top: 11px solid $brand-light;
        -webkit-filter: drop-shadow(0 2px $grey-5);
      }
    }
  }

  &.m-a-popover-top-left {
    .m-a-popover-content {
      bottom: 100%;
      left: 0;
      margin-bottom: 11px;

      &:after {
        top: 100%;
        left: 20px;
        border-left: 11px solid transparent;
        border-right: 11px solid transparent;
        border-top: 11px solid $brand-light;
        -webkit-filter: drop-shadow(0 2px $grey-5);
      }
    }
  }

  &.m-a-popover-top-right {
    .m-a-popover-content {
      bottom: 100%;
      right: 0;
      margin-bottom: 11px;

      &:after {
        top: 100%;
        right: 20px;
        border-left: 11px solid transparent;
        border-right: 11px solid transparent;
        border-top: 11px solid $brand-light;
        -webkit-filter: drop-shadow(0 2px $grey-5);
      }
    }
  }

  &.m-a-popover-bottom-right {
    .m-a-popover-content {
      top: 100%;
      right: 0;
      margin-top: 11px;

      &:after {
        bottom: 100%;
        right: 20px;
        border-left: 11px solid transparent;
        border-right: 11px solid transparent;
        border-bottom: 11px solid $brand-light;
        -webkit-filter: drop-shadow(0 -2px $grey-5);
      }
    }
  }

  &.m-a-popover-bottom-left {
    .m-a-popover-content {
      top: 100%;
      left: 0;
      margin-top: 11px;

      &:after {
        bottom: 100%;
        left: 20px;
        border-left: 11px solid transparent;
        border-right: 11px solid transparent;
        border-bottom: 11px solid $brand-light;
        -webkit-filter: drop-shadow(0 -2px $grey-5);
      }
    }
  }

  &:hover {
    .m-a-popover-content {
      visibility: visible;
    }
  }
}

.m-a-unset-line-height {
  line-height: unset !important;
}

.m-a-revert-all {
  all: revert !important;
}

.m-a-resize-textarea {
  resize: revert !important;
}

.custom-icon-heart, .custom-icon-heart-filled, .custom-icon-group-gift, .custom-icon-group-gift-filled {
  i {
    height: 21px;
    width: 21px;
    background-size: 20px;
    background-repeat: no-repeat;
    background-position: center;
    background-color: transparent;
  }
}

.custom-icon-heart {
  i {
    background-image: url($cdn-icon-heart-svg);
  }
}

.custom-icon-heart-filled {
  i {
    background-image: url($cdn-icon-heart-filled-svg);
  }
}

.custom-icon-group-gift {
  i {
    background-image: url($icon-group-gift);
  }
}

.custom-icon-group-gift-filled {
  i {
    background-image: url($cdn-icon-group-gift-selected-svg);
  }
}

.custom-icon-camera {
  height: 21px;
  width: 21px;
  background-size: 20px !important;
  background-repeat: no-repeat !important;
  background-position: center !important;
  opacity: 0.7;
  background-image: url($icon-camera-svg) !important;
}

#back-top {
  right: 3% !important;
  left: unset !important;
  bottom: 13% !important;
}

.ma-product-img-default {
  background-color: $grey-5;
  object-fit: contain !important;
  padding: 2 * $spacer;
}

@include media-breakpoint-down(sm){
  #gorgias-chat-container{
    #chat-button{
      bottom: 10% !important;
    }
  }
}