@import "src/assets/scss/colors.scss";
@import "src/assets/scss/custom-variables.scss";

.track-gifts-thank-you-list {
  border-radius: 4px;
  box-shadow: $mbr-box-shadow;
  border: solid 1px $grey-6;
  background-color: $brand-light;

  .track-gifts-thank-you-desc {
    color: $grey-1;
  }

  .track-gifts-thank-you-link {
    &, &:active, &:hover, &:visited {
      color: $text-link;
    }
  }
}