@import "src/assets/scss/colors.scss";
@import "src/assets/scss/mixins.scss";
@import "../../../../assets/scss/breakpoints.scss";

.track-gifts-search-container {
  position: relative;
  width: 60%;
  @include prefix(transition, width ease .4s, (ms, webkit));
  
  @include media-breakpoint-down(md) {
    width: 100%;
  }

  &.track-gifts-search-container-focus {
    width: 100%;
  }

  .track-gifts-search-input {
    border-radius: 2px;
    //TODO: Update to global color
    border: solid 1px #dfe1e7;
    background-color: $brand-light;
    outline: none;


    &::placeholder, &::-ms-input-placeholder, &:-ms-input-placeholder {
      color: $grey-3;
      opacity: 1; /* Firefox */
    }

    &:focus, &:active {
      border-color: $grey-0
    }
  }

  .track-gifts-search-icon {
    position: absolute;
    top: 50%;
    left: 0;
    margin: 0 12px;
    //TODO: Update to global color
    color: #989fae;
    cursor: pointer;
    @include prefix(transform, translateY(-50%), (ms, webkit));
  }

  .track-gifts-search-input-link {
    position: absolute;
    top: 50%;
    right: 12px;
    @include prefix(transform, translateY(-50%), (ms, webkit));

    &,
    &:hover,
    &:active,
    &:visited {
      color: $text-link !important;
    }
  }
}