@import "../../../../assets/scss/colors.scss";
@import "../../../../assets/scss/cdn-assets-links.scss";

.gift-status-pill {
  border-radius: 50px;
  width: fit-content;
  color: $grey-0 !important;

  span {
    line-height: 1;
    padding: 4px 0;
  }
}

.purchased-pill {
  background-color: $brand-main;
}

.reserved-pill {
  background-color: $icon-warning;
}

.default-pill {
  background-color: $grey-6;
}

.group-gift-pill {
  background-color: $icon-light-info;
  position: relative;

  &:before {
    position: absolute;
    left: 10px;
    top: 4px;
    content: "";
    display: inline-block;
    background-image: url($cdn-icon-group-gift-selected-svg);
    background-size: 15px;
    height: 15px;
    width: 15px;
    background-repeat: no-repeat;
    background-position: center;
  }
}