@import "../../../../assets/scss/colors.scss";
@import "../../../../assets/scss/custom-variables.scss";
@import "../../../../assets/scss/icons.scss";
@import "../../../../assets/scss/breakpoints.scss";
@import "src/assets/scss/mixins.scss";
@import "../../../../assets/scss/cdn-assets-links.scss";

.variant-product-content {
  max-height: 56vh;
  overflow-y: auto;

  .variant-product-image {
    width: 100%;
    height: auto;
    border-radius: 6px;
    border: solid 1px #d2d3d5;
  }
  
  .product-variant-circle-disk {
    border-radius: 50%;
    border: 1.5px solid transparent;
    // background-color: yellow;

    .product-variant-image {
      border-radius: 50%;
      height: 24px;
      width: 24px;
    }
  }

  .product-variant-avalible {
    cursor: pointer;

    &:hover {
      border: 1.5px solid $grey-0;
    }
  }

  .product-variant-disabled {
    position: relative;
    cursor: not-allowed;

    &::after {
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: url($icon-soldout-variant) no-repeat 50%;
      z-index: 9999;
    }
  }
  
  .variant-container {
    display: flex;
  }
  
  .add-product-active-must-have {
    border: solid 1px $icon-error !important;
    background-color: $icon-light-error !important;
  }
  
  .add-product-active-group-gift {
    border: solid 1px $icon-info !important;
    background-color: $icon-light-info !important;
  }
  
  .active-variant-product {
    border: 1.5px solid $grey-0;
  }
  
  .read-more {
    color: $text-link;
  }
  
  .variant-product-image-container {
    border-right: 1px solid $grey-5;
  
    .image {
      border-bottom: 1px solid $grey-5;
    }
  
    @include media-breakpoint-down(sm) {
      border-right: none;
    }
  }
  
  .recommend-product-input-container {
    .recommend-product-input-label {
      color: $grey-1;
    }
  
    .recommend-product-input {
      border-radius: 4px;
      border: solid 1px $grey-5;
      outline: none;
      line-height: inherit;
      height: unset;
  
      &::placeholder {
        color: $grey-3;
      }
  
      &:hover,
      &:focus,
      &:active {
        border: solid 1px $grey-1;
      }
    }
  }
  
  .recommned-product-store-url-submit,
  .recommned-product-input-change-url {
    position: absolute;
    top: 50%;
    right: 2 * $spacer;
    line-height: 1;
    @include prefix(transform, translateY(-50%), (ms, webkit));
  }
  
  .recommned-product-store-url-submit {
    button {
      outline: none !important;
      border: none !important;
      cursor: pointer;
    }
  }
  
  .recommned-product-input-overlay {
    position: relative;
  
    .recommned-price-currency {
      position: absolute;
      left: 2 * $spacer;
      top: 50%;
      @include prefix(transform, translateY(-50%), (ms, webkit));
      color: $grey-3 !important;
    }
  }
  
  .variant-size-section {
    .size-section-pill {
      cursor: pointer;
      border-radius: 4px;
      border: solid 1px $grey-5;
      display: inline-block;
    }
  
    .active-size-variant {
      border: solid 1px $grey-1;
      background-color: $grey-7;
    }
  }
}