@import "../../../../assets/scss/colors.scss";
@import "../../../../assets/scss/custom-variables.scss";

.tour-card {
  background-color: $tiffany-blue;
  border-radius: 6px;
  box-shadow: $mbr-box-shadow;
  // width: 419.5px;
  max-width: 320px;

  .tour-card-dots {
    width: 8px;
    height: 8px;
    margin: 0 6px 0 0;
    opacity: 0.8;
    border-radius: 8px;
    border: solid 1px $brand-light;

    &.visited {
      background-color: $brand-light;
    }
  }

  .tour-card-number {
    color: $brand-light;
    // cursor: pointer;
  }

  .tour-card-btn {
    color: $grey-1;
    border-radius: 2px;
    background-color: $brand-light;
    width: fit-content;
    cursor: pointer;
  }
}

#___reactour {
  svg {
    height: 100% !important;
    width: 100% !important;
  }
}