@import "../../../../assets/scss/colors.scss";

.more-option {
  float: left;

  .more-option-dropbtn {
    all:unset;
    cursor: pointer;
    color: $grey-4;
    font-size: 25px;
    //height: 36px;
    //width: 36px;
    border: none;
    outline: none !important;
    background: none;

    &:focus{
      background-color: $grey-5;
      color: $grey-0;
      border-radius: 4px;
    }

    &:focus ~ .more-option-dropdown{
        display: block;
    }
  }

  .more-option-dropdown {
    position: relative;
    display: none;

    &:hover{
      display: block;
    }

    .more-option-content {
      position: absolute;
      background-color: white;
      border: 1px solid $grey-4;
      border-radius: 6px;
      right: 0;
      width: max-content;
      z-index: 250;

      ul {
        list-style-type: none;

        li {
          background-color: #fff;

          &:hover {
            background-color: $grey-6;
          }

          .more-option-item {
            background-color: inherit;
            width: 100%;
            text-align: left;
            border: none;
            outline: none
          }
        }
      }
    }
  }
}

.more-option-disable {
  opacity: 0.5 !important;
  background-color: $grey-7 !important;
  border-radius: 4px;
  color: initial !important;
  cursor: none;
}
