@import "src/assets/scss/colors.scss";

.MA-loader {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 99999;
  // background-color: rgba(255, 255, 255, 0.7);
  background-color: rgba(0, 0, 0, 0.3);

  .MA-spinner {
    border: .2em solid $grey-5 !important;
    border-right-color: $grey-0 !important;
  }
}