@import "src/assets/scss/colors.scss";
@import "../../../../assets//scss/breakpoints.scss";

.track-gifts-buy-now {
  .track-gifts-buy-now-button {
    color: $grey-1 !important;
  }
}

.track-gifts-buy-now-modal {

  //Product detail
  .track-gifts-buy-now-product-detail {
    border-right: solid 1px $grey-5;
    @include media-breakpoint-down(sm) {
      border-right: none;
    }
  }

  // Inputs
  .track-gifts-buy-now-modal-input-container {
    .track-gifts-buy-now-label {
      color: $grey-1;

      &.track-gifts-buy-now-required {
        &:after {
          content: "*";
          margin-left: 3px;
          color: $input-required;
        }
      }
    }

    .track-gifts-buy-now-input {
      border-radius: 4px;
      border: solid 1px $grey-5;
      background-color: $brand-light;
      outline: none;

      &::placeholder {
        color: $grey-4;
      }

      &:active,
      &:focus {
        border-color: black;
      }

      &.track-gifts-buy-now-input-error{
        border-color: $input-required;
      }
    }

    .track-gifts-buy-now-input-error-text{
      color: $input-required;
    }
  }
}

.track-gifts-buy-now-modal-button {
  color: $grey-1 !important;
}
