@import "../../../../assets/scss/colors.scss";

.m-a-btn{
  border-radius: 2px;
  outline: none !important;
  border: none;
  color: $grey-1;

  &:disabled {
    background-color: $grey-5 !important;
    color: $grey-3;
  }

  &.m-a-primary-btn{
    background-color: $brand-main;

    &:hover{
      background-color: #89d6d7;
    }
  }

  &.m-a-secondary-btn{
    background-color: $grey-7;

    &:hover{
      background-color: $grey-6;
    }
  }
}