@import "../../assets/scss/colors.scss";

.m-a-opacity-1{
  opacity: 1 !important;
}

.personalise-toast-container {
  width: fit-content;
  width: -moz-fit-content;
  max-width: 527px;
  margin: 22px;

  .personalise-toast {
    &.Toastify__toast--info {
      background-color: $brand-main;
      color: $grey-1
    }
  }
}

.personalise-checkbox {

  cursor: pointer;
  display: inline-table;
  background-color: $brand-light;
  border: solid 1px $onboarding-border;
  border-radius: 5px;

  .personalise-checkbox-color{
    border-radius: 50%;
    height: 16px;
    width: 16px;
  }

  .personalise-checkbox-text{
    line-height: 0;
  }

  &:active {
    background-color: #ebfefe;
  }

  &:hover, &.personalise-checkbox-selected {
    margin: -1px;
    background-color: $onboarding-option-active-background;
    border-width: 2px;
    border-color: $onboarding-option-border-active;
  }

  .checkbox {
    display: flex;
    align-items: center;
    font-size: 15px;

    .checkbox__input {
      display: grid;
      grid-template-areas: "checkbox";

      >* {
        grid-area: checkbox;
      }

      input {
        opacity: 0;
        width: 20px;
        height: 20px;

        &:checked+.checkbox__control {
          background-color: $grey-1;

          i {
            transform: scale(1);
            color: white;
          }
        }
      }

      .radio__label {
        line-height: 0 !important;
      }

      .checkbox__control {
        display: inline-grid;
        width: 20px;
        height: 20px;
        border-radius: 0.25em;
        border: 0.1em solid $grey-3;

        i {
          transform: scale(0);
        }
      }

    }
  }
}

.personalise {
  .answer-selected {
    background-color: #ebfefe;
  }
}

.personalize-quiz-skip-question-link{
  text-decoration: underline;
  outline: none !important;
}