@import "src/assets/scss/colors.scss";

.track-gifts-pills {
  border-radius: 50px;
  line-height: 1;
  padding: 1px;

  &.track-gifts-purchased-pill, &.track-gifts-received-pill{
    background-color: $brand-main;
  }

  &.track-gifts-reserved-pill{
    background-color: $icon-warning;
  }

  &.track-gifts-available-pill{
    background-color: $grey-7;
  }
}