@import 'src/assets/scss/colors.scss';
@import 'src/assets/scss/mixins.scss';
@import 'src/assets/scss/custom-variables.scss';
@import '../../../../assets/scss/breakpoints.scss';
@import '../../../../assets/scss/cdn-assets-links.scss';

.m-a-toast-container {
  width: 601px !important;
  @include media-breakpoint-down(sm) {
    width: 90% !important;
    margin: 5% 5% 0 5%;
  }

  .m-a-toast {
    padding: (3 * $spacer);
    color: $grey-0;
    border-radius: 6px;
    box-shadow: $mbr-box-shadow;


    &:before {
      margin-right: (4 * $spacer);
      height: 60px;
      width: 60px;
    }

    &.Toastify__toast--success {
      background-color: $icon-success;

      &:before {
        content: url($il-success-svg);
      }

      &:hover {
        // TODO: update color to global color
        background-color: #7edd9e;
      }
    }

    &.Toastify__toast--error {
      background-color: $icon-error;

      &:before {
        content: url($il-error-svg);
      }
    }
  }
}