@import "../../../../assets/scss/colors.scss";
@import "../../../../assets/scss/custom-variables.scss";
@import "../../../../assets/scss/breakpoints.scss";
@import "../../../../assets/scss/cdn-assets-links.scss";

.card-item {
  border-radius: 12px;
  border: solid 1px $brand-light;
  background-color: $brand-light;
  overflow: visible;
  position: relative;

  .pointer {
    cursor: pointer;
  }

  .product-image {
    height: 200px;
    overflow: hidden;
    height: 280px;

    img {
      width: 100%;
      object-position: center;
      object-fit: cover;
      height: 280px;

      @include media-breakpoint-down(sm) {
        width: 100%;
      }
    }
  }

  .read-more {
    color: $text-link;
  }

  .product-bottom {
    // comment this if necessary
    .product-head-height{
      height: 100px;
      overflow: hidden;
    }

    .product-title {
      overflow: hidden;
      text-overflow: ellipsis;
      -webkit-line-clamp: 2;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      color: $grey-0;
      // height: 48px;

      @include media-breakpoint-down(sm) {
        height: 30px;
      }
    }

    .product-description {
      overflow: hidden;
      text-overflow: ellipsis;
      -webkit-line-clamp: 3;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      // height: 44px;
    }

    .product-sub-text-heading {
      color: $grey-1;
    }

    .product-sub-text-description {
      color: $grey-1;
      overflow: hidden;
      text-overflow: ellipsis;
      -webkit-line-clamp: 2;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      // height: 32px;
    }

    .product-price {
      .strike-price {
        font-size: 13px;
        line-height: 24px;
        color: $input-required;
        text-decoration: line-through;
      }
    }

    .add-btn {
      height: 44px;
      width: 44px;
      border-radius: 50px;
      background-color: #afe3e4;
      outline: none;
      border-width: 0px;

      background-image: url($cdn-icon-add-svg);
      background-repeat: no-repeat;
      background-size: 20px;
      background-position: center;

      &:hover,
      &:focus,
      &:active {
        background-color: $btn-primary-click;
      }
    }
  }

  .product-variant {
    border-top: solid 1px $grey-5;
    display: none;

    .product-variant-text {
      color: $grey-1;
    }

    .product-variant-circle-disk {
      border-radius: 50%;
      border: 1.5px solid transparent;
      // background-color: yellow;

      .product-variant-image {
        border-radius: 50%;
        height: 24px;
        width: 24px;
      }
    }

    .product-variant-avalible {
      cursor: pointer;

      &:hover {
        border: 1.5px solid $grey-0;
      }
    }

    .product-variant-disabled {
      position: relative;
      cursor: not-allowed;

      &::after {
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: url($icon-soldout-variant) no-repeat 50%;
        z-index: 9999;
      }
    }

    .active-variant-product {
      border: 1.5px solid $grey-0;
    }

    .size-section {
      width: 150px;

      .dd-wrapper {
        width: 100%;
        font-size: 15px;
        max-height: unset;

        .dd-header {
          outline: none;
          border-radius: 4px;
          border: solid 1px $grey-5;

          &:hover,
          &:active,
          &:focus {
            border-color: $grey-1;

            &~.dd-list {
              border-color: $grey-1;
            }
          }

          .fa {
            border-width: 0 3px 3px 0;
            display: inline-block;
            border-radius: 2px;
            margin-bottom: 7px;
            padding: 4px;
          }

        }

        .dd-list {
          border-top-left-radius: 4px;
          border-top-right-radius: 4px;
          position: absolute;
          left: 0;
          bottom: 100%;
          border: none;
          box-shadow: unset;
          // box-shadow: 0 8px 16px 0 rgba(74, 74, 74, 0.1), 0 0 1px 0 rgba(74, 74, 74, 0.15);
          font-weight: normal;
          border-left: solid 1px $grey-5;
          border-top: solid 1px $grey-5;
          border-right: solid 1px $grey-5;
          width: 100%;
          margin-bottom: -4px;
          max-height: 500px;

          .dd-scroll-list {
            max-height: 250px;
            padding: 0;
            overflow-y: hidden;

            .dd-list-item {
              font-size: 15px;
              padding: 8px 20px;
              outline: none;

              &.selected {
                background-color: $grey-6;
                color: $grey-0;
              }

              &:hover {
                background-color: $grey-6;
                color: $grey-0;
              }
            }
          }
        }
      }
    }
  }

  &:hover {
    box-shadow: $mbr-box-shadow;
    border: 1px solid $grey-5;
    background-color: $brand-light;

    .product-variant {
      display: block;
    }
  }
}