@import "src/assets/scss/colors.scss";

.guest-view-curator-names{
  color: $grey-1;
}

.guest-all-purchased-card {
  border-radius: 8px;
  box-shadow: 0 8px 16px 0 rgba(74, 74, 74, 0.1), 0 0 1px 0 rgba(74, 74, 74, 0.15);
  background-color: $brand-light;

  .guest-all-purchased-card-heading {
    font-size: 28px;
    max-width: 511px;
    color: $grey-0;
    line-height: 1.29;
  }

  .guest-all-purchased-card-link {
    cursor: pointer;
    
    &,
    &:hover,
    &:visited {
      color: $text-link
    }
  }

  .guest-all-purchased-card-txt{
    color: $grey-1;
  }

  .guest-all-purchased-card-btn {
    color: $grey-1 !important;
    background-color: $brand-main;
    text-decoration: none;
    cursor: pointer;

    &:hover {
      color: $grey-0 !important;
      background-color: $tiffany-blue;
    }
  }
}

.send-massagee-txt-secondary{
  color: $grey-3;
}

.guest-reserved-purchased-section {
  .title {
    color: $grey-0;
    border-bottom: 3px solid $grey-6;
  }
}