@import "../../../../assets/scss/colors.scss";
@import "../../../../assets/scss/typography.scss";
@import "../../../../assets/scss/mixins.scss";
@import "../../../../assets/scss/custom-variables.scss";
@import "../../../../assets/scss/breakpoints.scss";

.select-category-body {
  height: 70vh;
  overflow-y: auto;

  .category-columns {
    column-count: 3;
    -webkit-column-count: 3;
    -moz-column-count: 3;
    display: block;
    @include media-breakpoint-down(sm) {
      column-count: 1;
    -webkit-column-count: 1;
    -moz-column-count: 1;
    }

    .category-column {
      display: inline-block;

      // CUSTOM CHECKBOX START
      .checkbox {
        display: flex;
        align-items: center;
        font-size: 15px;
        cursor: pointer;

        .checkbox__input {
          display: grid;
          grid-template-areas: "checkbox";

          >* {
            grid-area: checkbox;
          }

          input {
            opacity: 0;
            width: 20px;
            height: 20px;
            cursor: pointer;

            &:checked+.checkbox__control {
              background-color: $grey-1;

              i {
                transform: scale(1);
                color: white;
              }
            }
          }

          .checkbox__control {
            display: inline-grid;
            width: 20px;
            height: 20px;
            border-radius: 0.25em;
            border: 0.1em solid $grey-3;

            i {
              transform: scale(0);
            }
          }

        }

        .select_category_radio__label {
          color: $grey-1 !important;
        }

        &:hover {
          background-color: $grey-7;
        }

      }
    }
    // CUSTOM CHECKBOX ENDS
  }

  .parent-category {
    color: $grey-1
  }

  .child-category {
    color: $grey-1;
    border: solid 1px transparent;

    &:hover {
      cursor: pointer;
      background-color: $grey-7;
      border-radius: 3px;
      border: solid 1px $grey-2;
    }
  }

  .selected-category {
    @extend .font-medium;
    background-color: $grey-7;
    border-radius: 3px;
  }
}

.categories-modal-footer{
 display: flex;
 justify-content: space-between;
 width: 100%;
 align-items: center;
}
.select-category-search-section {
  .search-category-input {
    line-height: inherit;
    border: solid 1px transparent;
    border-radius: 4px;
    width: 30px;
    @include prefix(transition, width ease .4s, (ms, webkit));

    &.search-category-input-focus {
      width: 420px;
      border-color: $grey-4;
      padding-right: (12 * $spacer) !important;

      &:focus {
        border-color: $grey-0;
      }
    }
  }

  .search-category-icon {
    position: absolute;
    top: 50%;
    left: 0;
    margin: 0 8px;
    color: $grey-4;
    cursor: pointer;
    @include prefix(transform, translateY(-50%), (ms, webkit));
  }

  .search-category-clear-link {
    display: none;
    position: absolute;
    top: 50%;
    right: 12px;
    @include prefix(transform, translateY(-50%), (ms, webkit));

    &,
    &:hover,
    &:active,
    &:visited {
      color: $text-link;
    }
  }
}