@import "../../../../assets/scss/colors.scss";

.edit-view-detail {
  .edit-view-detail-required::after {
    content: "*";
    color: $text-error;
  }

  .edit-view-detail-max-char {
    color: $grey-3
  }

  .resize-none {
    resize: none;
  }

  textarea {
    border-radius: 4px;
    border: solid 1px $grey-5;
    outline: none;
    height: unset;

    &::placeholder {
      color: $grey-3
    }
  }
}