@import '../../../assets/scss/colors.scss';
@import '../../../assets/scss/custom-variables.scss';
@import '../../../assets/scss/mixins.scss';
@import '../../../assets/scss/breakpoints.scss';
@import '../../../assets/scss/cdn-assets-links.scss';


.added-gift-must-have-btn {
  border: none;
  outline: none !important;
  border-radius: 4px;

  .added-gift-must-have-text {
    line-height: 2;
  }

}

.give-this-gift-button-container {
  @include media-breakpoint-down(sm) {
    position: absolute !important;
    right: 0;
  }
}

.guest-added-gift-must-have-active {
  background-color: $icon-light-error;
  border-radius: 4px;
  border: 0.8px solid $icon-error;
  color: $grey-0;
}

.added-gift-must-have-not-active {
  background-color: $grey-7;
  color: $grey-1;
}


.icon {
  height: 19px;
  width: 19px;
  background-size: 18px;
  background-repeat: no-repeat;
  background-position: center;
  background-color: transparent;
  border: none;
  outline: none !important;
}

.heart {
  background-image: url($cdn-icon-heart-svg);
}

.heart-fill {
  background-image: url($cdn-icon-heart-filled-svg);
}

.guest-added-card {
  border-top: 2px solid $grey-7;
  // border-bottom: 2px solid $grey-7;
  background-color: $brand-light;

  &:last-child {
    border-bottom: 2px solid $grey-7;
  }

  .category {
    color: $grey-1;
  }

  .guest-card-image {
    position: relative;

    .guest-must-have {
      position: absolute;
      top: -15px;
    }
  }

  .give-this-gift-btn {
    color: $grey-1 !important;
    background-color: $brand-light;
    border-radius: 2px;
    border: solid 1px $grey-5;
  }

  .strick-price {
    text-decoration: line-through;
    color: $input-required;
  }

  .quantity-mul-text {
    color: $grey-2;
  }

  .guest-toggle-pills {
    border-radius: 4px;
    padding-top: 2px;
    padding-bottom: 2px;
    border-radius: 20px;
    border: solid 1px transparent;
    background-color: $icon-light-info;

    i {
      color: $grey-4;
      font-size: 12px;
    }

    .guest-icon-active {
      background-image: url($cdn-icon-group-gift-selected-svg);
      background-size: 12px;
      height: 13px;
      width: 13px;
      background-repeat: no-repeat;
      background-position: center;
    }

    .guest-icon-text {
      color: $grey-0 !important;
      @include font(11px, 16px)
    }

    .received-amount {
      color: $grey-0 !important;
    }

    .total-amount {
      color: $grey-1 !important;
    }
  }

  .group-gift-background {
    position: relative;
    width: 250px;
    border-radius: 50px;
    height: 4px;
    background-color: $grey-7;
  }

  .group-gift-progress {
    position: absolute;
    top: 0;
    height: 4px;
    border-radius: 50px;
    background-color: $brand-main;
  }

  &:hover {
    background-color: #f8fdfd;

    .give-this-gift-btn {
      border: solid 1px $tiffany-blue;
    }
  }

  .guest-added-card-no-product-image {
    // TODO: Change this default image if necessary
    @include no-product-image(84px, 100%, 42px);

    // Custom added not present in design
    @include media-breakpoint-down(sm) {
      @include no-product-image(84px, 100%, 42px);
    }
  }
}

.product-img {
  max-width: 100%;
  height: 116px;
}

.added-card-recommended {
  .category-recommended-products {
    max-height: 80vh;
    overflow-y: auto;
  }

  .added-card-recommends-bottom-gradient {
    position: absolute;
    bottom: 0;
    height: 50px;
    width: 100%;
    background-image: linear-gradient(to top, $brand-light, rgba(255, 255, 255, 0));
  }
}

.added-card-other-category {
  color: $grey-1;
  background-color: $grey-7;
  border-top: 2px solid $grey-6;
  // border-bottom: 2px solid $grey-6;

  &:last-child {
    border-bottom: 2px solid $grey-6;
  }

  .title-link {
    cursor: pointer;
    color: $text-link;
  }

  &:hover {
    background-color: $grey-6;
  }
}

.guest-purchased-card {
  background-color: $grey-7;
  border-top: 2px solid $grey-6;
  // border-bottom: 2px solid $grey-6;

  .guest-purchased-card-title {
    color: $grey-0 !important;
  }

  &:hover {
    background-color: $grey-6;
  }
}