@import "../../../../assets/scss/colors.scss";
@import "../../../../assets/scss/custom-variables.scss";
@import "../../../../assets/scss/icons.scss";
@import "../../../../assets/scss/breakpoints.scss";
@import "../../../../assets/scss/cdn-assets-links.scss";

.recommended-cart {
  // border-radius: 12px;
  // box-shadow: $mbr-box-shadow;
  // border: 1px solid $grey-5;
  border-top: 2px solid $grey-7;
  // border-bottom: 2px solid $grey-7;
  background-color: $brand-light;

  &:last-child {
    border-bottom: 2px solid $grey-7;
  }

  .title {
    flex-grow: 3;
  }

  .sub-title {
    color: $grey-1;
  }

  .sub-title-progess {
    color: $grey-0;
    background-color: $grey-6;
    padding-top: 1px;
    padding-bottom: 1px;
  }

  .add-button {
    background-color: $grey-7;
    color: $grey-1;
    outline: none;
    border: none;
  }

  .card-view {
    border-radius: 12px;
    border: solid 1px $grey-5;
    background-color: $brand-light;
  }

  .cart-view:not(:last-child) {
    border-right: 1px solid $grey-5;
  }

  .card-buttom {
    background-color: $grey-7;
    border-radius: 8px;
    height: 92px;
    overflow: hidden;

    .card-buttom-subtext {
      color: $grey-1;
    }

    .card-buttom-title{
      color: $grey-0 !important;
    }

    .blog-img {
      width: auto;
      height: 92px;
      background-color: $grey-7;

      @include media-breakpoint-down(sm) {
        height: 54px;
      }
    }

    .read-more {
      color: $text-link;
    }

    &:hover {
      border-radius: 12px;
      box-shadow: $mbr-box-shadow;
      background-color: #fbfbfb;
    }
  }

  .slick-slider {
    .slick-list {
      overflow-y: visible !important;
    }

    .slick-slide {
      div {
        outline: none;
      }
    }

    .slick-track {
      margin-left: unset;
    }

    .slick-next,
    .slick-prev {
      width: 44px;
      height: 100%;
      z-index: 9;

      .slick-icon {
        width: 44px;
        height: 44px;
        padding: 10px;
        border-radius: 50px;
        box-shadow: 0 8px 16px 0 rgba(74, 74, 74, 0.1), 0 0 1px 0 rgba(74, 74, 74, 0.15);
        border: 1px solid $grey-5;
        background-color: $grey-5;
        color: $grey-1;
        background-color: $grey-5;
        font-family: inherit;
        font-size: 20px;
        color: $grey-1;
        opacity: 1;
      }
    }

    .slick-prev {
      left: 0;
      border-radius: 0;
      background-image: linear-gradient(to right, $brand-light, rgba(255, 255, 255, 0));
    }

    .slick-next {
      right: 0;
      border-radius: 0;
      background-image: linear-gradient(to left, $brand-light, rgba(255, 255, 255, 0));
    }

    .slick-next::before,
    .slick-prev::before {
      content: unset;
    }

    .slick-disabled {
      display: none !important;
    }

    .slick-dots {
      li {
        padding: 0;
        margin: 0;

        button {
          border-radius: 0;
          background: none !important;
          border: none !important;

          &:before {
            font-size: 24px;
            font-family: inherit;
          }
        }
      }

      button {
        padding-top: 0;
      }

      li.slick-active {
        button {
          display: flex;

          &:before {
            content: "";
            background: none !important;
            border: none !important;
          }

          &:after {
            content: "";
            position: absolute;
            width: 14.4px;
            height: 6px;
            border-radius: 50px;
            background-color: $grey-1 !important;
          }
        }
      }

      @include media-breakpoint-down(sm) {
        position: relative;
      }
    }
  }

  .add-my-own-card {
    border-radius: 12px;
    border: solid 1px $grey-5;
    background-color: $brand-light;
    cursor: pointer;

    &:hover {
      border-radius: 12px;
      box-shadow: $mbr-box-shadow;
    }

    .add-my-own-button {
      height: 44px;
      width: 44px;
      border-radius: 50px;
      background-color: $grey-7;
      outline: none;
      border-width: 0px;

      background-image: url($cdn-icon-add-svg);
      background-repeat: no-repeat;
      background-size: 20px;
      background-position: center;
    }
  }
}

.add-my-own-title {
  background-color: $grey-7;
  border-radius: 8px;

  .redirect-category-header {
    color: $grey-0;
  }

  .redirect-text {
    color: $grey-1;
  }

  .redirect-btn {
    color: $grey-1 !important;
    background-color: $brand-main;
    text-decoration: none;
    cursor: pointer;
    display: block;
    width: max-content;

    &:hover {
      color: $grey-0 !important;
      background-color: $tiffany-blue;
    }
  }
}