@import "src/assets/scss/colors.scss";
@import "src/assets/scss/typography.scss";

.track-gift-view-by {
  .view-by-toggle-section {
    .view-by-toggle {
      display: inline-block;
      border: solid 1px $grey-5;
      color: $grey-0;
      cursor: pointer;

      &:first-child {
        border-top-left-radius: 4px;
      }

      &:last-child {
        border-top-right-radius: 4px;
      }

      &.active-view-toggle {
        @extend .font-medium;
         box-shadow: inset 0 -2px 0 0 #212b36;
      }
    }
  }
}