@import "../../../../assets/scss/colors.scss";
@import "../../../../assets/scss/cdn-assets-links.scss";

.view-mobile-filter {
	.view-filter-heading {
	  border-bottom: 1px solid $grey-5;
	  color: $grey-0;
	}
  
	.filter-clear-all {
	  color: $text-link;
	  cursor: pointer;
	}
  
	.filter-title {
	  color: $grey-0 !important;
	}
  
	.filter-item {
	  border-bottom: 1px solid $grey-5;
	}
	.fliter-checkbox-section {
	  .view-fliter-checkbox {
		display: flex;
		align-items: center;
		font-size: 11px;
		font-weight: unset;
  
		.radio__label {
		  color: #3c4049;
		  line-height: 1;
		}
  
		.checkbox__input {
		  display: grid;
		  grid-template-areas: "checkbox";
		  color: #3c4049;
  
		  > * {
			grid-area: checkbox;
		  }
  
		  input {
			opacity: 0;
			width: 15px;
			height: 15px;
  
			&:checked + .checkbox__control {
			  background-color: $grey-1;
  
			  i {
				transform: scale(1);
				color: white;
			  }
			}
		  }
  
		  .checkbox__control {
			display: inline-grid;
			width: 15px;
			height: 15px;
			border-radius: 0.25em;
			border: 0.1em solid $grey-3;
  
			i {
			  transform: scale(0);
			}
		  }
		}
	  }
	}
  
	.accordion-list {
	  width: 100%;
  
	  .card {
		border: none;
	  }
  
	  .card-header {
		background: unset;
		border: none;
		padding: 0;
		margin: 0;
		cursor: pointer;
	  }
  
	  .card-body {
		padding: 0;
		color: #4a4a4a;
	  }
  
	  .card-body-contain {
		// border-bottom: 1px solid$grey-5;
		cursor: pointer;
  
		.checklist-circular-progress {
		  height: 30px;
		  width: 30px;
		}
  
		&:hover {
		  font-weight: 700;
		}
  
		.progess-divison {
		  color: $grey-2;
		}
	  }
  
	  .card-sub-category {
		border-bottom: 1px solid$grey-5;
		cursor: pointer;
  
		.progess-divison {
		  color: $grey-2;
		}
  
		&:hover {
		  font-weight: 700;
		}
	  }
  
	  .sub-product-list {
		cursor: pointer;
  
		&:hover {
		  font-weight: 700;
		}
	  }
  
	  .sub-product-list:not(:first-child) {
		border-top: 1px solid $grey-5;
	  }
  
	  .card-progress {
		font-size: 13px;
		line-height: 1.54;
		color: #74787b;
	  }
  
	  .icon-done {
		height: 20px;
		width: 20px;
		background-image: url($icon-check-svg);
	  }
  
	  .icon-checklist {
		.checklist-icon-arrow {
		  height: 20px;
		  width: 20px;
		  background: #ffffff
			url($icon-chevron-right-svg)
			no-repeat center;
		  -webkit-transition-duration: 0.4s;
		  -moz-transition-duration: 0.4s;
		  -o-transition-duration: 0.4s;
		  transition-duration: 0.4s;
		  -webkit-transition-property: -webkit-transform;
		  -moz-transition-property: -moz-transform;
		  -o-transition-property: -o-transform;
		  transition-property: transform;
		  transform: rotate(0deg);
		  -webkit-transform: rotate(0deg);
		  -ms-transform: rotate(0deg);
		}
  
		.opened {
		  transform: rotate(90deg);
		  -webkit-transform: rotate(90deg);
		  -ms-transform: rotate(90deg);
		}
	  }
	}
  }
  