@import "../../../../assets/scss/colors.scss";

.filter-section-total-items {
  width: max-content;
  float: left;
}

.filtered-items {
  display: flow-root;

  .filter-clear-link {
    color: $text-link
  }
}

.filter-icon {
  font-size: 20px;
}

.search-icon-mobile{
  font-size: 20px;
}

.filter-sort-section {
  text-align: right;

  .sort-by-text {
    font-size: 14px;
    line-height: 20px;
    color: $grey-1;
  }

  .filter-add-gift-btn {
    text-align: left;
  }
}

.sort-filter-warning {
  background-color: #fff4d6;

  .sort-filter-warning-btn {
    color: $text-link;
    cursor: pointer;
  }
}