@import "../../assets/scss/colors.scss";
@import "../../assets/scss/custom-variables.scss";
@import "../../assets/scss/breakpoints.scss";

.track-gifts-container {
  .divider-div {
    width: 100%;
    border-bottom: 0.5px solid $grey-5;
  }

  .track-gifts-show-search-dialog {
    display: block !important;
  }

  .track-gifts-search-box-container {
    position: absolute;
    width: 90%;
    top: 30px;
    left: 10px;
    display: none;
    box-shadow: 0 4px 8px 0 rgba(74, 74, 74, 0.1), 0 0 1px 0 rgba(74, 74, 74, 0.15);
    border: solid 1px #d2d3d5;
    background-color: #ffffff;
    border-radius: 6px;
    z-index: 13;
  }

  .track-gifts-no-product-main {
    border-radius: 4px;
    border: solid 1px $grey-5;

    .track-gifts-no-product-text {
      color: $grey-1;
      width: 90%;

      @include media-breakpoint-up(md){
        width: 50%;
      }

      @include media-breakpoint-up(lg){
        width: 44%;
      }
    }
  }
}